import { Injectable } from '@angular/core';
import * as $ from 'jquery';
declare var drawChart: any;
import { BaseUrlService } from '../shared/base-url/base-url.service';
import { DashboardService } from './dashboard/dashboard.service';

@Injectable({
  providedIn: 'root'
})
export class DashboardChartService {

  constructor(
    private dashboardService: DashboardService,
    private baseUrlService: BaseUrlService
  ) { }


   showSelectedChart(selectedChart, chartId, isModal, dateRange, searchQuery, language) {
    if (selectedChart) {
        this.convertDataInObject(selectedChart);
        this.showESChart(selectedChart, chartId, isModal, dateRange, searchQuery, language);
    }
}

 showESChart(chart, chartId, isModal, dateRange, searchQuery, language) {

    const chartConfig = chart;
    chartConfig.chartPath = '#' + chartId + chartConfig.chartPath.substring(chartConfig.chartPath.indexOf(' '));
    chartConfig.ui.titlePosition = {};
    if (isModal) {
        chartConfig.ui.height = 420;
        chartConfig.ui.width = 800;
    } else {
        chartConfig.ui.height = $('#' + chart.id).height();
        chartConfig.ui.width = $('#' + chart.id).width();
    }
    chartConfig.ui.margine = {bottom: 5, left: 5, top: 5};
    if (!(chartConfig.serverConnection) || chartConfig.serverConnection.trim() === '') {
        // tslint:disable-next-line: max-line-length
        chartConfig.serverConnection = this.getContextPath() + 'api/t=' + this.dashboardService.getBpmTenant() + '/a=' + this.dashboardService.getBpmAppId() + '/';
    }
    chartConfig.query = chart.query;
    // this condition is updated due to RQ issue
    // if(!chartConfig.isCustomChart){
    chartConfig.fromDate = this.formatFromDateForES(dateRange);
    chartConfig.toDate = this.formatToDateForES(dateRange);
    chartConfig.query = searchQuery;
    // }
    chartConfig.locale = language;
    drawChart(chartConfig);
    // eval(chartConfig.drawFunc);
}

  formatFromDateForES(dateRange) {
    let fromPreFix = 'now-';
    if (dateRange.futureFromDate) {
            fromPreFix = 'now+';
        }
    return (fromPreFix + Math.abs(dateRange.fromHours) + 'h');
  }

  formatToDateForES(dateRange) {
      let toPreFix = 'now-';
      if (dateRange.futureToDate) {
          toPreFix = 'now+';
      }
      return (toPreFix + Math.abs(dateRange.toHours) + 'h');
  }

  convertDataInObject(obj) {
    for (const p in obj) {
        if (obj.hasOwnProperty(p) ) {
            if (typeof obj[p] === 'string') {
                if (obj[p].toLowerCase() === 'true') {
                    obj[p] = true;
                } else if (obj[p].toLowerCase() === 'false') {
                    obj[p] = false;
                } else if (this.isNumeric(obj[p])) {
                    obj[p] = parseFloat(obj[p]);
                }
            } else if (typeof obj[p] === 'object' && p === 'ui') {
                this.convertDataInObject(obj[p]);
            }
        }
    }
  }

   isNumeric(n) {
      return !isNaN(parseFloat(n)) && isFinite(n);
  }

  getContextPath() {
    return this.baseUrlService.baseUrl + '/';
  }
}
