import { Component, Input } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { SlapService } from 'ng-bpm';

@Component({
  selector: 'pup-warning-dialog',
  templateUrl: './warning-dialog.component.html',
  styleUrls: ['./warning-dialog.component.css']
})
export class WarningDialogComponent {
  @Input() sessionTimeoutWarningSeconds = 5 * 60;
  @Input()
  set countdownSeconds(countdownSeconds: number){
    this.count = countdownSeconds;
    this.counter = this.secondsToMinSec(countdownSeconds);
  }
  get countdownSeconds(): number { return this.count; }

  constructor(
     private modalService: NgbModal,
     private slapService: SlapService,
  ) { }

  public counter: string;
  private count: number;

//  public timeoutSeconds = environment.sessionTimeoutWarningSeconds;

  continueSession() {
    this.slapService.queryTheServer();
    this.modalService.dismissAll();
  }

  private secondsToMinSec(seconds) {
    const minutes = Math.floor(seconds / (60));
    seconds -= minutes * (60);

    if(minutes === 0){
      return seconds+' second(s)';
    } else if(minutes > 0){
      return `${minutes} minute(s) and ${seconds} second(s)`;
    }
  }

}
