   addType(chartList) {
      const modifiedChartList = [];
      _.each(chartList, (value) => {
         modifiedChartList.push({ id: value.id, name: value.name, type: 4, isCustomChart: value.isCustomChart });
      });
      this.panel = modifiedChartList;
   }

   updateStandartCharts(theCharts) {
      const standardChartList = [];
      _.each(theCharts, (value) => {
         value.isCustomChart = false;
         standardChartList.push(value);
      });
      return standardChartList;
   }

   addCustomCharts(customCharts, theCharts) {
      const getSortOrder = (prop) => {
         return (a, b) => {
            if (a[prop] > b[prop]) {
               return 1;
            } else if (a[prop] < b[prop]) {
               return -1;
            }
            return 0;
         };
      };
      // tslint:disable-next-line: prefer-for-of
      for (let k = 0; k < customCharts.length; k++) {
         const customeChart = customCharts[k];
         customeChart.isCustomChart = true;
         theCharts.push(customeChart);
      }
      return theCharts.sort(getSortOrder('name'));
   }

