   /** geterate dashboard id  */
    genrateDBId() {
       let flag = true;
       let i = 1;
       while (flag) {
         const id = this.dashboardDetailList.dashboardDetail.length + i++;
         const foundItem = _.filter(this.panelsList, { dashboardId: 'dashboard' + id }, true);
         if (foundItem.length === 0) {
               flag = false;
               return id;
           }
         if (i > 1000) {
               flag = false;
               return this.panelsList.length + i + 1000;
           }
       }
   }

   /** create new dashboard */
    addDashboard() {
       if (this.paneledit) {
         const dashboard = this.selectedDashboard.dashboardName;
         const modalInstance = this.showConfirmation(this.getTranslatedValue('dashboard.saveDashboard'));
         modalInstance.result.then( () => {
               this.saveNewDashboard();
           },  () => {
               _.each(this.panelsList,  (value, key) => {
                   if (value !== undefined && value.dashboardName === dashboard) {
                       this.panelsList.splice(key, 1);
                   }
               });
           });
         if (this.selectedDashboard.isNew) {} else {
               this.cancelDBPanel(this.selectedDashboard);
           }
       }
       if (this.panelsList.length < this.config.dashboard_count) {
           const id = this.genrateDBId();
           const dashboardId = 'dashboard' + id;
           this.indexInfo[dashboardId] = {id: dashboardId};
           const panelLayout = {
                               top: {show: true},
                               middle: {panel: []},
                               bottom: {show: true, panel: []}
                           };
           const newDashboard = {dashboardId: 'dashboard' + id, dashboardName: 'Dashboard ' + id,
                               fileName: 'Dashboard' + id + '.json', panelLayout, isNew: true, index: id - 1};
           this.panelsList.push(newDashboard);
           this.selectedDashboard = newDashboard;
           this.customChartPanels = [];
           this.processPanelResponse(panelLayout);
           this.modalpanel = this.modalpanel.slice();
         //   this.parentdb.loadBottomPanel(this.modalpanel);
           this.addNewPanel();
           this.paneledit = true;
       } else if (this.panelsList.length === this.config.dashboard_count) {
           this.showInformation(
               this.getTranslatedValue('dashboard.dasboardMaxCountMsg', {dasboardMaxCountTran: this.config.dashboard_count,
                                  appNameTran: this.appId}));
       }
   }

   /** add new middle panel */
   addDBWidget(panelSelected) {
      const newWidget = {
         id: panelSelected.id,
         name: panelSelected.name,
         type: panelSelected.type,
         isCustomChart: panelSelected.isCustomChart,
         width: 6,
         height: 2,
         x: this.maxX,
         y: this.maxY,
         customChartId: ''
      };
      if (this.maxX === 6) { this.maxX = 0; this.maxY += 0; } else { this.maxX = 6; this.maxY += 0; }
      if (panelSelected.isCustomChart) {
         const customChartId = this.getCustomeChartId();
         newWidget.customChartId = customChartId;
         this.customChartPanels[customChartId] = panelSelected.id;
      } else {
         newWidget.isCustomChart = false;
      }
      this.removeAddnewPanel();
      this.widgets.push(newWidget);
      this.addNewPanel();
      this.isAddMiddlePanel = false;
   }

   /** add new bottom panel */
    addBottomWidget(panelSelected) {
       if (this.modalpanel.length < this.config.botttom_panel_count) {
           const newWidget = {
               id: panelSelected.id,
               name: panelSelected.name,
               isCustomChart: panelSelected.isCustomChart,
               customChartId: ''
           };
           if (panelSelected.isCustomChart) {
               const customChartId = this.getCustomeChartId();
               newWidget.customChartId = customChartId;
               this.customChartPanels[customChartId] = panelSelected.id;
           } else {
            newWidget.isCustomChart = false;
           }
           this.modalpanel.push(newWidget);
           this.modalpanel = this.modalpanel.slice();
           this.isAddBottomPanel = false;
         //   this.parentdb.loadBottomPanel(this.modalpanel);
           this.addingNewDash = true; // Temp added have to remove it.
       } else {
           // tslint:disable-next-line: max-line-length
           this.showInformation(this.getTranslatedValue('dashboard.maxBottomCountMsg', {maxBottomCountTran: this.config.botttom_panel_count}));
       }
   }

