    updateJsonForSave(reqType) {
       const newDashboardList = {dashboardDetail: []};
       _.each(this.dashboardDetailList.dashboardDetail,  (value) =>  {
           const dashboardVal = {dashboardId: '', dashboardName: '', fileName: '', isDeleted: false};
           dashboardVal.dashboardId = value.dashboardId;
           dashboardVal.dashboardName = value.dashboardName;
           dashboardVal.fileName = value.fileName;
           dashboardVal.isDeleted = value.isDeleted;
           if (value.dashboardId === this.selectedDashboard.dashboardId && reqType === 'delete') {
               dashboardVal.isDeleted = true;
            }
           newDashboardList.dashboardDetail.push(dashboardVal);
       });
       this.dashboardDetailList = newDashboardList;
   }

   /** save dashboard with all panels */
    async saveNewDashboard() {
       this.isNewDashboard = true;
       this.removeAddnewPanel();

       const middlePanelWidgets = [];
       _.each(this.widgets, (widget) => {
            const panel = {
               id: widget.id,
               name: widget.id,
               isCustomChart: widget.isCustomChart,
               width: widget.width,
               height: widget.height,
               x: widget.x,
               y: widget.y,
               customChartId: ''
           };
            if (widget.isCustomChart) {
               panel.customChartId = widget.customChartId;
           }
            if (panel.id !== 'addnew') {
                  middlePanelWidgets.push(panel);
            }
       });

       const bottomPanelWidgets = [];
       _.each(this.modalpanel, (widget) => {
           const panel = {
               id: widget.id,
               isCustomChart: widget.isCustomChart,
               customChartId: ''
           };
           if (widget.isCustomChart) {
               panel.customChartId = widget.customChartId;
           }
           bottomPanelWidgets.push(panel);
       });

       const topPanelWidgets = [];
       _.each(this.kpiwidgets, (widget) => {
           topPanelWidgets.push({ id: widget.id
           });
       });

       this.selectedDashboard.panelLayout.top.panel = topPanelWidgets;
       this.selectedDashboard.panelLayout.middle.panel = middlePanelWidgets;
       this.selectedDashboard.panelLayout.bottom.panel = bottomPanelWidgets;

       this.isModifyDashboard = false;
       const dashboard = this.selectedDashboard;
       _.each(this.dashboardDetailList.dashboardDetail,  (value) => {
           if (dashboard.dashboardId ===  value.dashboardId) {
               this.selectedDashboard.fileName = value.fileName;
               this.isModifyDashboard = true;
           }
       });

       if (!this.isModifyDashboard) {
           this.panelsList.push(this.dashboardService.updateDashboardDetailJSon(dashboard));
           this.dashboardDetailList.dashboardDetail.push(this.dashboardService.updateDashboardDetailJSon(dashboard));
           await this.saveDashboardDetail('save');
       } else {
         await this.saveDashboardDetail('modify');
       }


       delete this.selectedDashboard.isNew;
       delete this.selectedDashboard.index;
       await this.saveDashboard();
       this.paneledit = false;
       this.editPanelFlag();
   }

    saveDashboardDetail(reqType) {
       this.updateJsonForSave(reqType);
       this.dashboardService.saveDashboardDetail(this.dashboardDetailList, this.tenantName, this.appId)
       .subscribe( (data) => {
         console.log(data);
         //   showSuccess("The dashboard '" + this.selectedDashboard + "' has been saved successfully");
         if (reqType === 'delete') {
               this.dashboardService.setSelectedDashboard(undefined);
            } else {
            this.dashboardService.setSelectedDashboard(this.selectedDashboard.dashboardId);
            }
         this.getDashboards(this.tenantName, this.appId);
       });
      //  .error( (data, status) {
      //      this.$emit('bpmErrorEvent', data);
      //  })
   }

    saveDashboard() {
       this.dashboardService.saveDashboard(this.selectedDashboard.fileName,
       this.dashboardService.updateDashboardJson(this.selectedDashboard), this.tenantName, this.appId)
       .subscribe( (data) => {
          console.log(data);
          this.showSuccess('The dashboard \'' + this.selectedDashboard + '\' has been saved successfully');
       });
      //  .error( (data, status) {
      //      this.$emit('bpmErrorEvent', data);
      //  })
   }

   /** select dashboard for display */
    selectDashboard(dashboard) {
       this.dashboardService.setSelectedDashboard(dashboard.dashboardId);
       if (dashboard.dashboardId !== this.selectedDashboard.dashboardId) {
           if (this.paneledit ) {
               const modalInstance = this.showConfirmation(this.getTranslatedValue('dashboard.saveChangesConfirmation'));
               modalInstance.result.then( () => {
                   this.saveNewDashboard();
                   this.selectedDashboard = dashboard;
               },  () => {
                   this.cancelDBPanel(dashboard);
               });
           } else {
               this.loadDashboard(dashboard.fileName);
               this.editPanelFlag();
           }
       }
   }

   isActiveDashboard(dashboardId) {
     return this.selectedDashboard.dashboardId === dashboardId;
   }

    filterTopPanel(panel) {
       // tslint:disable-next-line: prefer-for-of
       for (let i = 0 ; i < this.kpiwidgets.length; i++) {
           const widget =  this.kpiwidgets[i];
           if (widget.id === panel.id) {
               return false;
           }
       }
       return true;
   }

    filterMiddlePanel(panel) {
       // tslint:disable-next-line: prefer-for-of
       for (let i = 0 ; i < this.widgets.length; i++) {
           const widget =  this.widgets[i];
           if (widget.id === panel.id) {
               return false;
           }
       }
       return true;
   }

    filterBottomPanel(panel) {
       // tslint:disable-next-line: prefer-for-of
       for (let i = 0 ; i < this.modalpanel.length; i++) {
           const widget =  this.modalpanel[i];
           if (widget.id === panel.id) {
               return false;
           }
       }
       return true;
   }

   removeWidget(panel) {
       if (this.paneledit) {
           const index = this.widgets.indexOf(panel);
           this.widgets.splice(index, 1);
           if (panel.isCustomChart) {
               delete this.customChartPanels[panel.customChartId];
           }
       }
   }

    removeTopPanel(panel) {
       if (this.kpiwidgets) {
           const index = this.kpiwidgets.indexOf(panel);
           this.kpiwidgets.splice(index, 1);
       }
   }

    removeBottomPanel(panel) {
       if (this.modalpanel) {
           const index = this.modalpanel.indexOf(panel);
           this.modalpanel.splice(index, 1);
           this.modalpanel = this.modalpanel.slice();
         //   this.parentdb.loadBottomPanel(this.modalpanel);
           if (panel.isCustomChart) {
               delete this.customChartPanels[panel.customChartId];
           }
       }
   }

   getDashboards(tenantName, appId) {
      this.selectedDashboardId = this.dashboardService.getSelectedDashboard();
      this.dashboardService.getDashboards(tenantName, appId)
         .subscribe((response) => {
            this.dashboardDetailList = response;
            this.panelsList = this.dashboardService.filterDashboardData(response);
            _.each(this.panelsList, (value) => {
               this.indexInfo[value.dashboardId] = { id: value.dashboardId };
               if (this.panelsList.length > 0 && this.selectedDashboardId === value.dashboardId) {
                  this.loadDashboard(value.fileName);
               }
            });
            if (this.selectedDashboardId === undefined) {
               this.loadDashboard(this.panelsList[0].fileName);
            }
         });
      // .error( (data, status, headers, config) {
      //     this.$emit("Dashboard Detail Error", data);
      // });
   }

