   dateRange(dateRange) {
      const today = new Date();
      return {
         fromHours: moment(today).diff(moment(dateRange.startDate),
            'days') * 24, futureFromDate: this.isFutureDate(moment(dateRange.startDate)),
         toHours: moment(today).diff(moment(dateRange.endDate), 'days') * 24,
         futureToDate: this.isFutureDate(moment(dateRange.endDate))
      };
   }

   isFutureDate(date) {
      return moment(new Date()) <= moment(date);
   }

   dateRangeForPdf(dateRange) {
      return { fromDate: moment(dateRange.startDate).format('DD-MMM-YYYY'), toDate: moment(dateRange.endDate).format('DD-MMM-YYYY') };
   }

