   delayTimeByIndex(kpi) {
      let esIndex = kpi.es_index;
      if (!esIndex) {
         esIndex = kpi.es_index_a === 'wi' ? kpi.es_index_a : kpi.es_index_b;
      }
      switch (esIndex) {
         case ('hist'):
            return '30 seconds delay';
         case ('ai'):
            return '30 seconds delay';
         case ('pi'):
            return '30 seconds delay';
         case ('wi'):
            return '15 minutes delay';
         case ('pc'):
            return '1 hour delay';
         case ('daily'):
            return '1 daily delay';
      }
   }

   mapEsIndexWithChart(){
      this.widgets.map(item => {
         const esIndexObject = this.dashboardService.bpmChartList.filter( chart => item.id === chart.id);
         item.es_index = esIndexObject[0].esIndex.substring(esIndexObject[0].esIndex.lastIndexOf('/') + 1, esIndexObject[0].esIndex.length);
      })
   }

