import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class RestGenericService {

  constructor(
    private http: HttpClient
  ) { }

  public httpGet(url: string): Observable<any> {
    const responseType: any =  'text';
    const headers = new HttpHeaders();
    headers.append('Content-Type', 'application/x-www-form-urlencoded');
    const options = {
      headers,
      withCredentials : true,
      responseType,
    };
    return this.http.get(url, options);
  }


  public httpPost(url: string, data: any): Observable<any> {
    const responseType: any = 'json';
    const headers = new HttpHeaders();
    headers.append('Content-Type', 'text/plain; charset=utf-8; application/json');
    const options = {
      headers,
      withCredentials : true,
      responseType
    };
    return this.http.post(url, data, options);
  }
  
   // This PUTs the given parameterData JSON object as a JSON object in the request body
    putJsonRequest(url: string, data: any): Observable<any>{
      const responseType: any = 'json';
      const headers = new HttpHeaders();
      headers.append('Content-Type', 'text/plain; charset=utf-8; application/json');
      const options = {
        headers,
        withCredentials : true,
        responseType
      };
      return this.http.put(url, data, options);
}
}
