import { Injectable } from '@angular/core';
import { BpmFilterByListField, BpmSqlOperator } from 'ng-bpm';
import { NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import moment from 'moment';
import { DateRange } from '../../dates/dates.service';

export enum RangeType {
  NONE,
  RANGE,
  SINCE,
  UNTIL
}

@Injectable({
  providedIn: 'root'
})
export class FilterDateService {

  public getDateRangeFilters(dateRange: DateRange, listField): BpmFilterByListField[] {
    const filters: BpmFilterByListField[] = [];

    if (dateRange && dateRange.from && dateRange.to) {
      const momentDateFrom = this.getMomentDate(dateRange.from);
      filters.push(this.getRangeStartFilter(momentDateFrom, listField));
      const momentDateTo = this.getMomentDate(dateRange.to);
      filters.push(this.getRangeEndFilter(momentDateTo, listField));
    }

    return filters;
  }

  public getBasicDateFilters(basicDate: NgbDateStruct, rangeType: RangeType, listField): BpmFilterByListField[] {
    const filters: BpmFilterByListField[] = [];
    if (basicDate) {
      const momentDate = this.getMomentDate(basicDate);
      if (rangeType === RangeType.SINCE) {
        filters.push(this.getRangeStartFilter(momentDate, listField));
      } else if (rangeType === RangeType.UNTIL) {
        filters.push(this.getRangeEndFilter(momentDate, listField));
      } else {
        console.error('Unexpected error in date handling, Range or None should be impossible');
      }
    }
    return filters;
  }

  private getRangeStartFilter(momentDate, listField): BpmFilterByListField {
    return  {
      listField,
      sqlOperator: BpmSqlOperator.SQLOP_GREATERTHANOREQUALTO,
      value: String(momentDate.startOf('day').valueOf()),
    };
  }

  private getRangeEndFilter(momentDate, listField) {
    return  {
      listField,
      sqlOperator: BpmSqlOperator.SQLOP_LESSTHANOREQUALTO,
      value: String(momentDate.endOf('day').valueOf()),
    };
  }

  private getMomentDate(ngbDate: NgbDateStruct): any {
    return moment([ngbDate.year, ngbDate.month - 1, ngbDate.day]);
  }

}
