import { Injectable } from '@angular/core';
import { NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { TimedoutDialogComponent } from './timedout-dialog/timedout-dialog.component';
import { WarningDialogComponent } from './warning-dialog/warning-dialog.component';
import { DEFAULT_INTERRUPTSOURCES } from './core/defaultinterruptsources';
import { Idle } from './core/idle';
import { Keepalive } from './core/keepalive';

@Injectable({
  providedIn: 'root'
})
export class IdleUserService {

  idleState = 'Not started.';
  timedOut = false;
  lastPing?: Date = null;
  title = 'angular-idle-timeout';

  constructor(
    private idle: Idle,
    private keepalive: Keepalive,
    private modalService: NgbModal,
  ) {}

  private warningDialogModalRef: NgbModalRef;

  private reset() {
    this.idle.watch();
    this.idleState = 'Started.';
    this.timedOut = false;
  }

  private showIdleWarning() {
    this.idleState = `You've gone idle!`;

    this.warningDialogModalRef = this.modalService.open(WarningDialogComponent, {
      ariaLabelledBy: 'modal-basic-title',
      size: 'lg',
      scrollable: true
    });
    this.warningDialogModalRef.componentInstance.sessionTimeoutWarningSeconds = this.idle.getTimeout();
  }

  // The countdown in the Idle Warning Dialog has expired.
  private userTimedOut() {
    this.idleState = 'Timed out!';
    this.timedOut = true;
    this.modalService.dismissAll();
    const modalRef = this.modalService.open(TimedoutDialogComponent, {
      ariaLabelledBy: 'modal-basic-title',
      size: 'lg',
      scrollable: true
    });
    modalRef.result.then(
      () => this.reset(),
      () => this.reset()
    );
  }

  // This is called once by the App Component ngOnInit
  public userIdleActive (sessionIdleSeconds = 25 * 60, sessionTimeoutWarningSeconds = 5 * 60) {
    // The number of seconds the user doesn't interact with the GUI before we start the timeout.
    this.idle.setIdle(sessionIdleSeconds);
    // The number of seconds that we display the warning dialog.
    this.idle.setTimeout(sessionTimeoutWarningSeconds);
    // sets the default interrupts, in this case, things like clicks, scrolls, touches to the document
    this.idle.setInterrupts(DEFAULT_INTERRUPTSOURCES);

    this.idle.onIdleEnd.subscribe(() => {
      this.idleState = 'No longer idle.'
      this.reset();
    });

    this.idle.onTimeout.subscribe(() => this.userTimedOut());

    this.idle.onIdleStart.subscribe(() => this.showIdleWarning());

    this.idle.onTimeoutWarning.subscribe((countdown) => {
      this.warningDialogModalRef.componentInstance.countdownSeconds = countdown;
      this.idleState = `You will time out in ${countdown} seconds!`;
    });

    // sets the ping interval to 15 seconds
    this.keepalive.interval(15);

    this.keepalive.onPing.subscribe(() => this.lastPing = new Date());

    this.reset();
  }

}
