import { Injectable, Injector } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { DashboardMessageBoxComponent } from './dashboard-message-box.component';

@Injectable({
  providedIn: 'root'
})
export class DashboardMessageService {

  constructor(private modalService: NgbModal,
    private injector: Injector) { }

   showError(message, stack) {
    var msg = null;
    if(stack === undefined) {
        msg = {"type": "errors", "message":message};
    } else {
        msg = {"type": "errorWithStack", "message":message, "stack": stack};
    }
    return this.showMessage(msg);
}

 showSuccess(message) {
    var msg = {"type": "success", "message":message};
    return this.showMessage(msg);
}

 showConfirmation(message) {
    var msg = {"type": "confirmation", "message":message};
    return this.showMessage(msg);
}

 showInformation(message) {
    var msg = {"type": "information", "message":message};
    return this.showMessage(msg);
}

 showDataTable(message) {
    var msg = {"type": "datatable", "message":message};
    return this.showMessage(msg);
}

 showMessage(msg) {
    // return $uibModal.open({
    //     animation: true,
    //     templateUrl: "gui/messageBox.html",
    //     controller: "messageBoxCtrl",
    //     bindToController: true,
    //     windowClass: 'z-index: 99000 !important;',
    //     size: "lg",
    //     resolve:{
    //         msg: function() {
    //             return msg;
    //         }
    //     }
    // });          
    
    this.modalService = this.injector.get(NgbModal);
    const modalRef = this.modalService.open(DashboardMessageBoxComponent,
      { ariaLabelledBy: 'modal-basic-title', scrollable: true });
      //Commnted as getting build failure
    modalRef.componentInstance.msg = msg;
    return modalRef;
}
}
