import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { PaginationService } from '../shared/service/pagination.service';

@Component({
  selector: 'pup-pagination',
  templateUrl: './pagination.component.html',
  styleUrls: ['./pagination.component.css']
})
export class PaginationComponent implements OnInit, OnChanges {
  pager :any  = {};
  constructor(private paginationService: PaginationService) { }
  @Input () pageDetail;
  @Output () broadcastPageInfo = new EventEmitter();

  ngOnInit(): void {
  }

  ngOnChanges(changes) {
    if (this.pageDetail) {
      this.setPageDetailChanges(changes);
    }
  }

  setPageDetailChanges(changes) {
    if (changes['pageDetail']['previousValue']) {
      if (changes['pageDetail']['previousValue']['filter'] !== changes['pageDetail']['currentValue']['filter'] &&
      this.pageDetail.filter!=null && this.pageDetail.filter!=='' ) {
        this.refreshPageIndex(1);
      }
      if (changes['pageDetail']['previousValue']['totalItems'] !== changes['pageDetail']['currentValue']['totalItems'] &&
      this.pageDetail.totalItems!=null && this.pageDetail.totalItems!=='') {
        this.refreshPageIndex(this.pager.hasOwnProperty('currentPage') ? this.pager.currentPage : 1);
      }
      if (changes['pageDetail']['previousValue']['pageSize'] !== changes['pageDetail']['currentValue']['pageSize'] &&
      this.pageDetail.totalItems!=null && this.pageDetail.totalItems!=='') {
        this.refreshPageIndex(1);
      }
    } else {
      this.refreshPageIndex(1);
    }
  }

  setPage(page) {
    if (page < 1) {// || page > this.pager.totalPages
        return;
    }
    this.pager = this.paginationService.getPage(this.pageDetail.totalItems, page, this.pageDetail.pageSize);
    this.broadcastPageInfo.emit({ pager: this.pager });
}

refreshPageIndex(pageIndex) {
  this.pager = this.paginationService.getPage(this.pageDetail.totalItems, pageIndex, this.pageDetail.pageSize);
}

}
