import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import {BpmServiceResponse, BpmWorkItemDetail} from 'ng-bpm';
import { Observable, of } from 'rxjs';
import { catchError, flatMap, tap } from 'rxjs/operators';
import { BpmConnectionService } from '../shared/bpm-connection/bpm-connection.service';
import { MultiBpmServerService } from '../multi-server/multi-bpm-server.service';
import { BpmServerService } from '../shared/bpm-server/bpm-server.service';

@Component({
  selector: 'pup-work-item-detail',
  templateUrl: './work-item-detail.component.html',
  styleUrls: ['./work-item-detail.component.css']
})
export class WorkItemDetailComponent implements OnInit {
  public workItemDetail$: Observable<BpmWorkItemDetail>;
  public showProcessGraph = false;
  public workItemId: number;

  private workItem: BpmWorkItemDetail;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private bpmConnectionService: BpmConnectionService,
    private multiBpmServerService: MultiBpmServerService,
    private bpmServerService: BpmServerService,
  ) { }

  ngOnInit() {
    this.workItemDetail$ = this.getWorkItemDetail().pipe(
      tap((workItemDetail: BpmWorkItemDetail) => this.workItem = workItemDetail)
    );
  }

  private getWorkItemDetail(): Observable<BpmWorkItemDetail> {
    if (this.multiBpmServerService.isMulti()) {
      return this.getWorkItemDetailMulti();
    }
    this.workItemId = +this.route.snapshot.paramMap.get('wiId');
    return this.getWorkItemDetailSingle(this.workItemId);
  }

  private getWorkItemDetailSingle(workItemId: number): Observable<BpmWorkItemDetail> {
    return this.bpmConnectionService.getBpmConnectionApplication().pipe(
      flatMap(bpmConnectionApplication =>
        this.bpmServerService.getWorkItemDetailById(bpmConnectionApplication, workItemId))
    );
  }

  showProcessGraphClicked() {
    this.showProcessGraph = true;
  }

  hideProcessGraphClicked() {
    this.showProcessGraph = false;
  }

  madeChoice(event) {
      this.router.navigate([ '/wiList/'], { queryParamsHandling: 'merge' });
  }

  workItemAction(event: Observable<BpmWorkItemDetail>) {
    this.workItemDetail$ = event.pipe(
      catchError((error) => {
        console.error('error', error);
        return of(this.workItem);
      }),
      tap((workItemDetail: BpmWorkItemDetail) => this.workItem = workItemDetail),
    );
  }

  // in multi-server the ID of the Work Item is not enough to identify it
  // So in multi server, the Work List will have stored the selected work item
  private getWorkItemDetailMulti(): Observable<BpmWorkItemDetail> {
    return this.bpmServerService.getWorkItemDetail(this.multiBpmServerService.selectedWorkItem);
  }

  private subscribeAndNavigate(obs: Observable<any>) {
    obs.subscribe();
    this.router.navigate([ '/wiList/'], { queryParamsHandling: 'merge' });
  }

  public reassign(event: Observable<BpmServiceResponse>) {
    this.subscribeAndNavigate(event);
  }

  public subtask(event: Observable<BpmWorkItemDetail>) {
    this.subscribeAndNavigate(event);
  }

}
