import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
  selector: 'pup-show-udas-checkbox',
  templateUrl: './show-udas-checkbox.component.html',
  styleUrls: ['./show-udas-checkbox.component.css']
})
export class ShowUdasCheckboxComponent {
  @Input() showUdas = false;
  @Output() showUdasChange = new EventEmitter<boolean>();

  public showUdasClicked() {
    this.showUdasChange.emit(this.showUdas);
  }
}
