import { Component, OnInit, Injector } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { IdleUserService } from './idle-time-out/idle-user.service';
import { BaseUrlService } from './shared/base-url/base-url.service';
import { ConnectionManagerService } from 'ng-bpm';
import { environment } from '../environments/environment';

@Component({
  selector: 'pup-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  constructor(
    private translate: TranslateService,
    private baseUrlService: BaseUrlService,
    private idleUserService: IdleUserService,
    private connectionManagerService: ConnectionManagerService,
  ) {
    this.translate.addLangs(['en', 'fr']);
    this.translate.setDefaultLang('en');
    const browserLang = translate.getBrowserLang();
    translate.use(browserLang.match(/en|fr/) ? browserLang : 'en');
  }

  ngOnInit() {
    this.idleUserService.userIdleActive(environment.sessionIdleSeconds, environment.sessionTimeoutWarningSeconds);
    this.connectionManagerService.getLoginInfo(this.baseUrlService.baseUrl).subscribe();
  }

}
