import { Component, EventEmitter, Output } from '@angular/core';
import { BpmFilterByUda, BpmSqlOperator, BpmUdaType } from 'ng-bpm';
import {faTrash} from "@fortawesome/free-solid-svg-icons";

@Component({
  selector: 'pup-filter-uda',
  templateUrl: './filter-uda.component.html',
  styleUrls: ['./filter-uda.component.css']
})
export class FilterUdaComponent {
  @Output() filtersChange = new EventEmitter<BpmFilterByUda[]>();

  public filters: BpmFilterByUda[] = [];
  public faTrash = faTrash;

  public removeFilter(index) {
    this.filters.splice(index, 1);
    this.modelChange();
  }

  public addUdaFilter() {
    const udaFilter: BpmFilterByUda = {
      udaName: '',
      udaType: BpmUdaType.TYPE_STRING,
      sqlOperator: BpmSqlOperator.SQLOP_EQUALTO,
      value: ''
    };

    this.filters.push(udaFilter);
    this.modelChange();
  }

  public modelChange() {
    this.filtersChange.emit(this.filters);
  }
}
