import { Component } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { SlapService } from 'ng-bpm';

@Component({
  selector: 'pup-timedout-dialog',
  templateUrl: './timedout-dialog.component.html',
  styleUrls: ['./timedout-dialog.component.css']
})
export class TimedoutDialogComponent {

  constructor(
    private modalService: NgbModal,
    private slapService: SlapService,
  ) { }

  loginUser() {
    this.slapService.queryTheServer();
    this.modalService.dismissAll();
  }

}
