import { Component, OnInit, Output , EventEmitter } from '@angular/core';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'pup-ag-grid-page-size',
  templateUrl: './ag-grid-page-size.component.html',
  styleUrls: ['./ag-grid-page-size.component.css']
})
export class AgGridPageSizeComponent implements OnInit {
  @Output() pageSizeChange = new EventEmitter<any>();

  selectedValue: any = environment.gridPageSize;

  constructor() {
    this.selectedValue = localStorage.getItem('pageSize') || environment.gridPageSize;
   }

  ngOnInit() {
    this.pageSizeChange.emit(this.selectedValue);
  }

  onPageSizeChanged() {
    localStorage.setItem('pageSize', this.selectedValue);
    this.pageSizeChange.emit(this.selectedValue);
  }

}
