import { Component, EventEmitter, Output } from '@angular/core';
import { BpmSortByUda, BpmUdaType } from 'ng-bpm';
import { faTrash } from '@fortawesome/free-solid-svg-icons';

@Component({
  selector: 'pup-sort-uda',
  templateUrl: './sort-uda.component.html',
  styleUrls: ['./sort-uda.component.css']
})
export class SortUdaComponent {
  @Output() sortUdasChange = new EventEmitter<BpmSortByUda[]>();

  public sortBys: BpmSortByUda[] = [];
  public faTrash = faTrash;

  public removeSortBy(index) {
    this.sortBys.splice(index, 1);
    this.modelChange();
  }

  public addUdaSort() {
    const udaSort: BpmSortByUda = {
      udaName: '',
      udaType: BpmUdaType.TYPE_STRING,
      sortOrder: true
    };

    this.sortBys.push(udaSort);
    this.modelChange();
  }

  public reverseSortOrder(udaSort: BpmSortByUda) {
    udaSort.sortOrder = !udaSort.sortOrder;
    this.modelChange();
  }

  public modelChange() {
    this.sortUdasChange.emit(this.sortBys);
  }
}
