import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router} from '@angular/router';
import { BpmProcessInstanceAction, BpmProcessInstanceDetail, BpmWorkItemDetail } from 'ng-bpm';
import { BaseUrlService } from '../shared/base-url/base-url.service';
import { MultiBpmServerService } from '../multi-server/multi-bpm-server.service';
import { Observable } from 'rxjs';
import { flatMap } from 'rxjs/operators';
import { BpmConnectionService } from '../shared/bpm-connection/bpm-connection.service';
import { BpmServerService } from '../shared/bpm-server/bpm-server.service';
import { DashboardMessageService } from '../dashboard/dashboard-message-box/dashboard-message.service';

@Component({
  selector: 'pup-process-instance-detail',
  templateUrl: './process-instance-detail.component.html',
  styleUrls: ['./process-instance-detail.component.css']
})
export class ProcessInstanceDetailComponent implements OnInit {
  processInstance;
  processInstanceId;
  graphPiid;
  processInstanceHistory: any[]  = [];
  piHistoryJson0: any = {
    additionalInfo: {},
    consumerId: 181,
    consumerType: 7,
    errorMessage: '',
    eventCode: 0,
    eventType: '__ProcessCreated',
    handledCode: 4,
    historyId: 191,
    producerId: 181,
    producerType: 7,
    responsible: 'admin',
    timestamp: 1506501098713,
    trackedUDAs: []
};

piHistoryJson1: any = {
    additionalInfo: {},
    consumerId: 181,
    consumerType: 7,
    errorMessage: '',
    eventCode: 0,
    eventType: '__UDATracking',
    handledCode: 4,
    historyId: 192,
    producerId: 181,
    producerType: 7,
    responsible: 'admin',
    timestamp: 1506501098716,
    trackedUDAs: []
};
showError = false;
  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private bpmServerService: BpmServerService,
    private baseUrlService: BaseUrlService,
    private bpmConnectionService: BpmConnectionService,
    private multiBpmServerService: MultiBpmServerService,
    private dashboardMessageService: DashboardMessageService
  ) { }

  ngOnInit() {
    this.getProcessInstanceDetail();
  }

  private getProcessInstanceDetail() {
    if (this.multiBpmServerService.isMulti()) {
      this.getProcessInstanceDetailMulti().subscribe(result => {
        this.processInstance = result;
        this.getProcessInstanceHistory();
      });
    } else {
      this.getProcessInstanceDetailSingle().subscribe(result => {
        this.processInstance = result;
        this.getProcessInstanceHistory();
      });
    }

    this.processInstanceHistory.push(this.piHistoryJson0);
    this.processInstanceHistory.push(this.piHistoryJson1);
  }

  private getProcessInstanceDetailSingle(): Observable<BpmProcessInstanceDetail> {
    this.processInstanceId = this.route.snapshot.paramMap.get('idInst');
    return this.bpmConnectionService.getBpmConnectionApplication().pipe(
      flatMap(bpmConnectionApplication =>
        this.bpmServerService.getProcessInstanceDetailById(bpmConnectionApplication, this.processInstanceId))
    );
  }

  // in multi-server the ID of the process instance is not enough to identify it
  // So in multi server, the Process List will have stored the selected process instance
  private getProcessInstanceDetailMulti(): Observable<BpmProcessInstanceDetail> {
    return this.bpmServerService.getProcessInstanceDetail(this.multiBpmServerService.selectedProcessInstance);
  }

  errorList(obj) {
    const ret = [];
    if (obj && obj.error) {
        const tmp = obj.error.split('{');
        tmp.forEach( (item) => {
            while (item.indexOf('}') >= 0) {
                item = item.replace('}', '');
            }
            ret.push(item);
        });
    }
    return ret;
  }

  processInstanceAction(event: BpmProcessInstanceAction) {
    if (event === BpmProcessInstanceAction.Suspend) {
      this.bpmServerService.suspend(this.processInstance).subscribe(data => this.processInstance = data);
    } else if (event === BpmProcessInstanceAction.Resume) {
      this.bpmServerService.resume(this.processInstance).subscribe(data => this.processInstance = data);
    } else if (event === BpmProcessInstanceAction.Abort) {
      this.bpmServerService.abort(this.processInstance).subscribe(data => this.processInstance = data);
    }
  }

  showProcessGraph() {
    this.graphPiid = this.processInstanceId;
  }

  hideProcessGraph() {
    this.graphPiid = null;
  }

  bpmOnClicked(wi) {
   this.router.navigateByUrl('#/wi/' + wi.id);
  }

  getIcon(node) {
    if (0 === node.type) {
        return './assets/images/s_start.png';
    }
    if (1 === node.type) {
        return './assets/images/s_exit.png';
    }
    if (2 === node.type) {
        return './assets/images/s_task.png';
    }
    if (6 === node.type) {
        return './assets/images/s_or.png';
    }
    if (5 === node.type) {
        return './assets/images/s_condition.png';
    }
    if (4 === node.type) {
        return './assets/images/s_and.png';
    }
    if (7 === node.type || 9 === node.type) {
        return './assets/images/s_subProcess.png';
    }
    return './assets/images/s_task.png';
  }

  getNodeInstance(nodeid) {
    return this.processInstance.nodes.find((nodeInstance) => nodeInstance.idDef === nodeid);
  }

  getProcessInstanceHistory() {
    this.bpmServerService.getProcessInstanceHistory(this.processInstance).subscribe(
      histories => this.processInstanceHistory = histories
    );
  }

  updateProcessInstance(updatedProcessInstance) {
    this.processInstance = updatedProcessInstance;
  }

  currentActivityClicked(wi) {
    wi.instanceId = this.processInstanceId;
    this.router.navigate(['/wi/' + wi.id], { queryParamsHandling: 'merge' });
  }

  // in multi-server the ID of the Work Item is not enough to identify it
  // So in multi server, the Work List will have stored the selected work item
  public getWorkItemDetailMulti(): Observable<BpmWorkItemDetail> {
    return this.bpmServerService.getWorkItemDetail(this.multiBpmServerService.selectedWorkItem);
  }

  errorDetail(processInstance) {
    const message = this.errorList(processInstance)
    this.dashboardMessageService.showInformation(message);
    this.showError = !this.showError;
  }

}
