import { Component, OnInit, Input } from '@angular/core';
import {TranslateService} from '@ngx-translate/core';
import _ from 'underscore';
import { DashboardChartService } from '../dashboard-chart.service';
import { DashboardService } from '../dashboard/dashboard.service';
declare var NVD3Charts: any;
declare var getNormalizedChartData: any;
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import {faDownload, faFilePdf, faTable} from '@fortawesome/free-solid-svg-icons';
import { DashboardMessageService } from '../dashboard-message-box/dashboard-message.service';

@Component({
  selector: 'pup-dashboard-modal-popup',
  templateUrl: './dashboard-modal-popup.component.html',
  styleUrls: ['./dashboard-modal-popup.component.css']
})
export class DashboardModalPopupComponent implements OnInit {
  @Input() id;
  @Input() isPopUp = false;
  chartId = 'chart';
  title = '';
  isModal = false;
  isCustomChart = false;
  pdfId = '';

  public faTable = faTable;
  public faDownload = faDownload;
  public faFilePdf = faFilePdf;

  constructor(
    private dashboardService: DashboardService,
    private translate: TranslateService,
    private chartsService: DashboardChartService,
    private activeModal: NgbActiveModal,
    private dashboardMessageService: DashboardMessageService

  ) { }

  ngOnInit() {
    this.getChartConfigs();
  }

   getChartConfigs() {
    const customChart = this.dashboardService.getCustomChartMap[this.id];
    const chartList = this.dashboardService.getBpmChartList();
    if (chartList.length > 0) {
        this.chartId = 'chart' + this.dashboardService.getChartPanelCurrentNum();
        if (!this.isModal) {
          this.dashboardService.setChartPanelCurrentNum(this.dashboardService.getChartPanelCurrentNum() + 1 );
          }
        if (customChart === undefined) {
            this.genrateChart(chartList, this.id);
        } else {
            this.isCustomChart = true;
            this.genrateChart(chartList, customChart);
        }
    }
}

 genrateChart(chartList, chartId){
    _.forEach(chartList,  (value) => {
        if (value.id === chartId) {
            this.title = value.name;
            this.pdfId = value.id;
            // tslint:disable-next-line: max-line-length
            this.chartsService.showSelectedChart(value, this.chartId, this.isModal, this.dashboardService.getSelectedDateRange(), this.dashboardService.getSearchQuery(), this.translate.currentLang);
        }
    });
}

getChartType(chartId) {
    const chartList = this.dashboardService.getBpmChartList();
    // tslint:disable-next-line: prefer-for-of
    for (let i = 0; i < chartList.length; i++) {
        if (chartList[i].id == chartId) {
            return chartList[i].chartType;
        }
    }
    return '';
}

exportToPDF(chartId, chartName, isCustomChart) {
    // tslint:disable-next-line: max-line-length
    this.dashboardService.exportToPDF(chartId, chartName, false, this.dashboardService.getPopupPDFInfo().appId, this.dashboardService.getPopupPDFInfo().tenantId, this.dashboardService.getPopupPDFInfo().dateForPDF, isCustomChart);
}

exportToCSV(chartId) {
    this.dashboardService.exportToCSV(chartId);
}

showDataTable(chartId) {
    const chartList = this.dashboardService.getBpmChartList();
    _.forEach(chartList,  (value) => {
        if (value.id === chartId) {
            new NVD3Charts(null);
            const dataTable = getNormalizedChartData(value);
            this.dashboardMessageService.showDataTable(dataTable);
        }
    });
}

ok() {
    this.activeModal.close();
}

cancel() {
    this.activeModal.close('cancel');
}

}
