import { Component, OnInit, Input } from '@angular/core';
import _ from 'underscore';
import { DashboardChartService } from '../dashboard-chart.service';
import { BpmServerService } from '../../shared/bpm-server/bpm-server.service';
import { RestGenericService } from '../../shared/service/rest-generic.service';
import { BaseUrlService } from '../../shared/base-url/base-url.service';
import {DashboardService} from '../dashboard/dashboard.service';

@Component({
  selector: 'pup-dashboard-kpi-summary',
  templateUrl: './dashboard-kpi-summary.component.html',
  styleUrls: ['./dashboard-kpi-summary.component.css']
})
export class DashboardKpiSummaryComponent implements OnInit {
  @Input() id;
  public userId;
  public kpiValue;
  public bpmApplication$;
  public application;
  public tenant;
  activityId = '';
  constructor(
    private dashboardService: DashboardService,
    private chartService: DashboardChartService,
    private bpmServerService: BpmServerService,
    private restGenericService: RestGenericService,
    private baseUrlService: BaseUrlService
  ) { }

  ngOnInit() {
    this.bpmApplication$ = this.bpmServerService.getApplication();
    this.bpmApplication$.subscribe(item => {
      this.application = item.applicationConnection.applicationId;
      this.tenant = item.applicationConnection.tenant;
      if (this.id === 4 || this.id === 5) {
        this.getMultipleKpiData();
      } else {
        this.getKpiData();
      }
    });

  }


  getKpiData() {
    if (this.dashboardService.getBpmKpiList().length > 0) {
      _.forEach(this.dashboardService.getBpmKpiList(), (value) => {
        if (value.id === this.id) {
          let query = value.es_query;
          query = JSON.stringify(query);
          const dateRange = this.dashboardService.getSelectedDateRange();
          query = query.replace('[[search_query]]', this.dashboardService.getSearchQuery());
          query = query.replace('[[begin_time]]', this.chartService.formatFromDateForES(dateRange));
          query = query.replace('[[end_time]]', this.chartService.formatToDateForES(dateRange));
          // query = query.replace("[[user_name]]", 'admin@us.fujitsu.com');
          query = query.replace('[[user_name]]', JSON.parse(sessionStorage.getItem('SSOFI_Logged_User')).userId);
          // query = query.replace("[[user_name]]",$scope.appState.userId);
          console.log('es_index::' + value.es_index + '    query ', query);
          query = JSON.parse(query);
          this.fetchKpiData(value.es_index, query)
            .subscribe((kpiSummary) => {
              if (kpiSummary.hasOwnProperty('hits')) {
                this.kpiValue = kpiSummary.hits.total;
              } else {
                this.kpiValue = 0;
              }
            });
          //   .error( function(data, status, headers, config) {
          //       $scope.kpiValue= "Data not found !!";
          //       $scope.$emit('bpmErrorEvent', data);
          //  });
        }
      });
    }



  }

  fetchKpiData(esIndex, esQuery) {

    const esUrl = this.baseUrlService.baseUrl + '/' + 'api/t=' + this.tenant + '/a=' + this.application + '//Analytics/' + esIndex;
    return this.restGenericService.httpPost(esUrl, esQuery);
  }

  updateQuery(query) {
    query = JSON.stringify(query);
    const dateRange = this.dashboardService.getSelectedDateRange();
    query = query.replace('[[search_query]]', this.dashboardService.getSearchQuery());
    query = query.replace('[[begin_time]]', this.chartService.formatFromDateForES(dateRange));
    query = query.replace('[[end_time]]', this.chartService.formatToDateForES(dateRange));
    // query = query.replace("[[user_name]]", 'admin@in.fujitsu.com');
    query = query.replace('[[user_name]]', JSON.parse(sessionStorage.getItem('SSOFI_Logged_User')).userId);
    query = query.replace('[[activity_id_list]]', this.activityId);
    console.log('    query ', query);
    query = JSON.parse(query);
    return query;
  }

  getMultipleKpiData() {
    if (this.dashboardService.getBpmKpiList().length > 0) {
      _.forEach(this.dashboardService.getBpmKpiList(), (value) => {
        if (value.id === this.id) {
          const query = this.updateQuery(value.es_query_a);
          this.fetchKpiData(value.es_index_a, query)
            .subscribe((kpiSummary) => {
              this.activityId = '';
              if (kpiSummary.hasOwnProperty('aggregations') && kpiSummary.aggregations.sample.buckets.length > 0) {
                _.forEach(kpiSummary.aggregations.sample.buckets, (value, key) => {
                  _.forEach(value.sample.buckets, (value, key) => {
                    this.activityId += value.key + ',';
                  });
                });
              }
              const queryData = this.updateQuery(value.es_query_b);
              this.fetchKpiData(value.es_index_b, queryData)
                .subscribe((kpiSummary) => {
                  if (kpiSummary.hasOwnProperty('hits')) {
                    this.kpiValue = kpiSummary.hits.total;
                  } else {
                    this.kpiValue = 0;
                  }
                });
            });
        }
      });
    }
  }

}
