import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DashboardComponent } from './dashboard/dashboard.component';
import { DashboardModalPanelComponent } from './dashboard-modal-panel/dashboard-modal-panel.component';
import { DashboardModalPopupComponent } from './dashboard-modal-popup/dashboard-modal-popup.component';
import { DashboardKpiSummaryComponent } from './dashboard-kpi-summary/dashboard-kpi-summary.component';
import { DashboardSearchFilterComponent } from './dashboard-search-filter/dashboard-search-filter.component';
import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { HttpTranslateLoaderFactory } from '../shared/http-translate-loader.factory';
import { HttpClient } from '@angular/common/http';
import { FormsModule } from '@angular/forms';
import { NvD3Module } from 'ng2-nvd3';
import { DatesModule } from '../dates/dates.module';
import { DashboardMessageBoxComponent } from './dashboard-message-box/dashboard-message-box.component';
import { GridsterModule } from 'angular-gridster2';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { Ng2SearchPipeModule } from 'ng2-search-filter';
import { ClipboardModule } from 'ngx-clipboard';

@NgModule({
  declarations: [
    DashboardComponent,
    DashboardModalPanelComponent,
    DashboardModalPopupComponent,
    DashboardKpiSummaryComponent,
    DashboardSearchFilterComponent,
    DashboardMessageBoxComponent,
  ],
  imports: [
    CommonModule,
    FormsModule,
    DatesModule,
    FontAwesomeModule,
    TranslateModule.forChild({
      loader: {
        provide: TranslateLoader,
        useFactory: (HttpTranslateLoaderFactory),
        deps: [HttpClient]
      }
    }),
    NvD3Module,
    GridsterModule,
    Ng2SearchPipeModule,
    ClipboardModule
  ],
  exports: [
    DashboardComponent,
    DashboardModalPanelComponent,
    DashboardModalPopupComponent,
    DashboardKpiSummaryComponent,
    DashboardSearchFilterComponent,
    DashboardMessageBoxComponent,
  ],
  entryComponents: [
    DashboardModalPopupComponent,
    DashboardMessageBoxComponent,
  ],
})
export class DashboardModule { }
