import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { BpmSqlOperator } from 'ng-bpm';
import { FilterService } from '../filter.service';

@Component({
  selector: 'pup-sql-operator-selector',
  templateUrl: './sql-operator-selector.component.html',
  styleUrls: ['./sql-operator-selector.component.css']
})
export class SqlOperatorSelectorComponent implements OnInit {
  @Input() sqlOperator: BpmSqlOperator;
  @Output() sqlOperatorChange = new EventEmitter<BpmSqlOperator>();

  public sqlOperators: any[];

  constructor(
    private filterService: FilterService,
  ) {}

  ngOnInit() {
    this.sqlOperators = this.filterService.enumToArray(BpmSqlOperator);
  }

  public modelChange() {
    this.sqlOperatorChange.emit(this.sqlOperator);
  }

}
