import { Injectable } from '@angular/core';
import { NgbCalendar, NgbDate } from '@ng-bootstrap/ng-bootstrap';

export interface DateRange {
  from: NgbDate;
  to: NgbDate;
}

@Injectable({
  providedIn: 'root'
})
export class DatesService {

  constructor(
    private ngbCalendar: NgbCalendar
  ) { }

  public today: NgbDate = JSON.parse(JSON.stringify(this.ngbCalendar.getToday()));

  public lastMonth(): DateRange {
    return {
        from: this.ngbCalendar.getPrev(this.today, 'd', 30),
        to: this.today,
    };
  }

  public lastWeek(): DateRange {
    return {
      from: this.ngbCalendar.getPrev(this.today, 'd', 7),
      to: this.today,
    };
  }

  public yesterday(): DateRange {
    return {
      from: this.ngbCalendar.getPrev(this.today),
      to: this.today,
    };
  }

  public isInside(date: NgbDate, dateRange: DateRange): boolean {
    return date.after(dateRange.from) && date.before(dateRange.to);
  }

}
