import { Injectable} from '@angular/core';
import { BpmFilterByListField, BpmPiConstant, BpmSqlOperator, BpmWiConstant } from 'ng-bpm';

export enum StateSelectorType {
  PROCESS_INSTANCE,
  WORK_ITEM,
  PROCESS_DEFINITION,
}

@Injectable({
  providedIn: 'root'
})
export class FilterStateService {

  // There can be zero or one filters.
  public getStateFilter(stateValues, stateType: StateSelectorType): BpmFilterByListField {
    if (!this.isAllOneValue(stateValues)) {
      if (stateType === StateSelectorType.PROCESS_INSTANCE) {
        return this.getProcessInstanceStateFilter(stateValues);
      }
      if (stateType === StateSelectorType.WORK_ITEM) {
        return this.getWorkItemStateFilter(stateValues);
      }
    }
    return undefined;
  }

  // If all states are true or they are all false, we assume the user wants to include all states.
  // So then we wouldn't add a filter.
  private isAllOneValue(booleanObject): boolean {
    // We would like to use Object.values here, but it isn't supported by IE.
    const keys = Object.keys(booleanObject);
    const compare = booleanObject[keys[0]];
    return keys.every(key => {
      return (compare === booleanObject[key]);
    });
  }

  private getCommaSeparatedStates(stateValues): string {
    const keys = Object.keys(stateValues);
    const states = keys.reduce((commaSeparatedStates: string, key: string) => {
      let suffix = '';
      if (stateValues[key]) {
        suffix = (commaSeparatedStates === '') ? key : ',' + key;
      }
      return commaSeparatedStates.concat(suffix);
    }, '');
    return '(' + states + ')';
  }

  private getProcessInstanceStateFilter(stateValues: any): BpmFilterByListField {
    const commaSeparatedStates = this.getCommaSeparatedStates(stateValues);
    return {
      listField: BpmPiConstant.piListField.STATE,
      sqlOperator: BpmSqlOperator.SQLOP_IN,
      value: commaSeparatedStates,
    };
  }

  private getWorkItemStateFilter(stateValues: any): BpmFilterByListField {
    const commaSeparatedStates = this.getCommaSeparatedStates(stateValues);
    return {
      listField: BpmWiConstant.wiListField.STATE,
      sqlOperator: BpmSqlOperator.SQLOP_IN,
      value: commaSeparatedStates,
    };
  }

}
