  filterDashboardData(data) {
     const dashboardList = [];
     if (data.dashboardDetail.length > 0) {
        _.each(data.dashboardDetail,  (value, key) => {
             if (!value.isDeleted) {
                 dashboardList.push(value);
             }
         });
     }
     return dashboardList;
 }

  exportToPDF(id, chartNameEn, isMiddleChartPdf, appId, tenantId, dateForPDF, isCustomChart) {
     const content = [];
     var docDefinition: any = this.pdfBody(appId, tenantId, dateForPDF);
     const chartName = this.translate.instant(!isCustomChart ?'charts.'+chartNameEn : chartNameEn);
     content[0] = this.getSearchFilter(dateForPDF, this.getSearchQuery());
     content[1] = {canvas: [{type: 'line',x1: 0,y1: 5,x2: 535,y2: 5,lineWidth: 0.5}]};
     content[2] = {text: chartName, style: 'header'};
     var svg;
     var chartList = this.getBpmChartList() || [];
     const getCurrentChart = (chartId) => {
         for(let i=0; i < chartList.length; i++) {
             let theChart = chartList[i];
             if(theChart.id == chartId) {
                 while(theChart.childChart && theChart.childChart.hasOwnProperty("chartData")) {
                     theChart = theChart.childChart;
                 }
                 return theChart;
             }
         }
     }
     var tableToArray = (tbl, opt_cellValueGetter) => {
         opt_cellValueGetter = opt_cellValueGetter || function (td)  {
             return td.textContent || td.innerText;
             };
         const twoD = [];
         for(var rowCount = tbl.rows.length, rowIndex = 0; rowIndex < rowCount; rowIndex++) {
             twoD.push([]);
         }
         for(let rowIndex = 0, tr; rowIndex < rowCount; rowIndex++) {
             const tr = tbl.rows[rowIndex];
             for(var colIndex = 0, colCount = tr.cells.length, offset = 0; colIndex < colCount; colIndex++) {
                 const td = tr.cells[colIndex], text = opt_cellValueGetter(td, colIndex, rowIndex, tbl);
                 while(twoD[rowIndex].hasOwnProperty(colIndex + offset)) {
                     offset++;
                 }
                 for(var i = 0, colSpan = parseInt(td.colSpan, 10) || 1; i < colSpan; i++) {
                     for(var j = 0, rowSpan = parseInt(td.rowSpan, 10) || 1; j < rowSpan; j++) {
                         twoD[rowIndex + j][colIndex + offset + i] = text;
                     }
                 }
             }
         }
         return twoD;
     }
     var theChart = getCurrentChart(id);

     if(theChart.chartType == "table" && document.getElementById("tablechart_" + theChart.id) != null) {
         var tableData = tableToArray(document.getElementById("tablechart_" + theChart.id), undefined);
         var pdfData = [];
         var rindex = 0;
         var rowData = [];
         if(theChart.ui.showTitleInTable) {
             rowData = [];
             rowData.push({text: tableData[0][0],style: 'tableHeader', colSpan: tableData[0].length, alignment: 'center'});
             for(var k=1; k < tableData[0].length; k++) {
                 rowData.push({});
             }
             rindex = rindex + 1;
             pdfData.push(rowData);
         }
         if(tableData.length > rindex) {
             rowData = [];
             for(var m=0; m < tableData[rindex].length; m++) {
                 rowData.push({text: tableData[rindex][m],style: 'tableHeader', alignment: 'center'});
             }
             rindex = rindex + 1;
             pdfData.push(rowData);
             for(var p=rindex; p<tableData.length; p++) {
                 rowData = [];
                 for(var mm=0; mm < tableData[p].length; mm++) {
                     rowData.push({text: tableData[p][mm],alignment: 'right'});
                 }
                 pdfData.push(rowData);
             }
         }
         content[3] = {
             table:
               {
                 headerRows: rindex,
                 body: pdfData
               }
         }
     } else {
         if(isMiddleChartPdf) {
            svg = document.querySelector('#export_'+id+' div svg');
         }

         else {
            svg = document.querySelector('#popup_'+id+' svg');
         }


         svgAsPngUri(svg, {canvg:canvg}, (uri) => {
            var $image = $('<img>'), image = $image[0];
            image.src = uri;

            $image.on('load', function() {
                var canvas = document.createElement('canvas'),
                    ctx = canvas.getContext('2d'),
                    imgWidth = image.width,
                    imgHeight = image.height;
                canvas.width = imgWidth;
                canvas.height = imgHeight;
                console.log(imgWidth,'<<< imgWidth  imgHeight>>>',imgHeight);
                ctx.drawImage(image, 0, 0, imgWidth, imgHeight);
                content[3] = {image: canvas.toDataURL('image/png'), fit: [540, 460]};
            });
        });
     }

     setTimeout( () => {
         docDefinition.content = content;
         pdfMake.createPdf(docDefinition).download(this.replaceSpace(chartName)+ "_" + Date.now() + ".pdf");
     }, 100);
 }

  exportToCSV(chartId) {
     const chartList = this.getBpmChartList();
     chartList.forEach((value) => {
         if (value.id === chartId) {
            saveChartToCSV(value);
         }
     });
 }

  replaceSpace(chartName) {
     return chartName.replace(new RegExp(' ', 'g'), '');
 }

