import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';


@Injectable({
  providedIn: 'root'
})
export class BaseUrlService {
  readonly baseUrl: string;

  constructor() {
    this.baseUrl =  this.getUrlSubPath();
  }

  private getUrlSubPath (): string {
    if (environment.url) {
      return environment.url;
    }

    // window.location.href returns something like:
    //  - Default PUP
    //    http://dxpdemo198:49950/aa2/gui-ng/#/piList?t=Default&a=System
    //  - If PUP is used as a custom application it will be called like this:
    //    http://dxpdemo198:49950/aa/api/t=Default/a=Test/ui/index.html#/

    // Get the URL before the hash
    //    http://dxpdemo198:49950/aa2/gui-ng/
    //    http://dxpdemo198:49950/aa/api/t=Default/a=Test/ui/index.html
    const urlPreHash = window.location.href.split('#')[0];

    if (urlPreHash.indexOf(environment.pupGuiUrlSuffix) >= 0) {
      // This is the default PUP
      // Get the URL before the hash and without gui-ng
      // For example:
      // http://dxpdemo198:49950/aa2/gui-ng/#/piList?t=Default&a=System
      // returns:
      // http://dxpdemo198:49950/aa2/
      return urlPreHash.split(environment.pupGuiUrlSuffix)[0];
    }

    // This is PUP being used as a custom application
    // Get the URL before the hash and without the api
    // For example:
    // http://dxpdemo198:49950/aa/api/t=Default/a=Test/ui/index.html#/
    // returns:
    // http://dxpdemo198:49950/aa/
    return urlPreHash.split('api')[0];

  }


}
