import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { BpmServerService } from '../shared/bpm-server/bpm-server.service';
import { DashboardService } from '../dashboard/dashboard/dashboard.service';
import {faCrosshairs, faCubes, faDesktop, faListAlt} from "@fortawesome/free-solid-svg-icons";
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
@Component({
  selector: 'pup-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  public bpmApplication$;
  public multiApplication = false;
  public application;
  public tenant;
  dashboardList = [];
  selectedDashboard;

  public faCrosshairs = faCrosshairs;
  public faListAlt = faListAlt;
  public faCubes = faCubes;
  public faDesktop = faDesktop;

  constructor(
    private route: ActivatedRoute,
    private bpmServerService: BpmServerService,
    private router: Router,
    private dashboardService: DashboardService,
    private modalService: NgbModal
  ) { }

  ngOnInit() {
    if (!this.bpmServerService.isMulti()) {
      this.bpmApplication$ = this.bpmServerService.getApplication();
      this.bpmApplication$.subscribe(item => {
        this.application = item.applicationConnection.applicationId;
        this.tenant = item.applicationConnection.tenant;
        this.getDashboards();
      });
    } else {
      this.multiApplication = true;
    }
  }

  public async navigateToMenu(navigate) {
    try {
      this.router.navigate([navigate], { queryParamsHandling: 'merge' });
      window.scrollTo(0, 0);
    } catch (error) {
    }
  }

  getDashboards() {
    try {
      this.dashboardService.getDashboards(this.tenant, this.application)
      .subscribe((response) => {
        this.dashboardList = this.dashboardService.filterDashboardData(response);
        this.selectedDashboard = this.dashboardList[0].dashboardId;
        this.dashboardService.setSelectedDashboard(this.dashboardList[0].dashboardId);
      },
      error => {  
        this.loadDefaultDashboardDetail();
        this.loadDefaultDashboardConfig();
        this.loadDefaultDashboard();
        this.modalService.dismissAll();
    });
    } catch (err) {
     
    }

  }

  loadDefaultDashboardDetail() {
    this.dashboardService.loadDefaultJson('DashboardDetail')
      .subscribe((response) => {
        this.dashboardList = this.dashboardService.filterDashboardData(response);
        this.selectedDashboard = this.dashboardList[0].dashboardId;
        this.dashboardService.setSelectedDashboard(this.dashboardList[0].dashboardId);
        this.dashboardService.saveDashboardDetail(response, this.tenant, this.application).subscribe();
      })

  }
  loadDefaultDashboardConfig() {
    this.dashboardService.loadDefaultJson('DashboardConfig')
      .subscribe((response) => {
        this.dashboardService.saveDashboardConfig(response, this.tenant, this.application).subscribe();
      })
  }

  loadDefaultDashboard(){
    this.dashboardService.loadDefaultJson('Dashboard1')
    .subscribe( (response) => {
        this.dashboardService.saveDashboard('Dashboard1.json', response, this.tenant, this.application).subscribe();
    })
    // .error( function(data, status, headers, config) {
    //     $scope.$emit("ShowError", data);
    // });
}

setSelectedDashboard () {
  this.dashboardService.setSelectedDashboard(this.selectedDashboard);
}

}
