import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { BpmUdaType } from 'ng-bpm';
import { FilterService } from '../filter.service';

@Component({
  selector: 'pup-uda-type-selector',
  templateUrl: './uda-type-selector.component.html',
  styleUrls: ['./uda-type-selector.component.css']
})
export class UdaTypeSelectorComponent implements OnInit {
  @Input() udaType: BpmUdaType;
  @Output() udaTypeChange = new EventEmitter<BpmUdaType>();

  public udaTypes: any[];

  constructor(
    private filterService: FilterService,
  ) {}

  ngOnInit() {
    this.udaTypes = this.filterService.enumToArray(BpmUdaType);
  }

  public modelChange() {
    this.udaTypeChange.emit(this.udaType);
  }

}
