import { Component, EventEmitter, Input, Output } from '@angular/core';
import { Observable } from 'rxjs';
import { BpmProcessInstanceDetail } from 'ng-bpm';

@Component({
  selector: 'pup-comments',
  templateUrl: './comments.component.html',
  styleUrls: ['./comments.component.css']
})
export class CommentsComponent {
  @Input() processInstance: BpmProcessInstanceDetail;
  @Output() processInstanceChange = new EventEmitter<BpmProcessInstanceDetail>();

  public addNewComment(event$: Observable<BpmProcessInstanceDetail>) {
    event$.subscribe(processInstanceDetail => {
      this.processInstance = processInstanceDetail;
      this.processInstanceChange.emit(this.processInstance);
    });
  }

}
