import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { BpmProcessDefinition } from 'ng-bpm';

@Component({
  selector: 'pup-process-definition-list-item',
  templateUrl: './pup-process-definition-list-item.component.html',
  styleUrls: ['./pup-process-definition-list-item.component.css']
})
export class PupProcessDefinitionListItemComponent {
  @Input() pd: BpmProcessDefinition;
  @Output() bpmClick = new EventEmitter<BpmProcessDefinition>();

  onClick() {
    this.bpmClick.emit(this.pd);
  }
}
