import { Component, OnInit } from '@angular/core';
import { BpmPiConstant, BpmWorkItem, WorkItemFilter, BpmListCriteria } from 'ng-bpm';
import { Observable } from 'rxjs';
import { PupPriorityTextComponent } from '../shared/component/process-instance.component';
import { DatePipe } from '@angular/common';
import { PupWorkItemNameColumnComponent, PupWorkItemStateComponent } from '../shared/component/work-item.component';
import { BpmServerService } from '../shared/bpm-server/bpm-server.service';
import { StateSelectorType } from '../filter/filter-state-selector/filter-state.service';
import { ShowUdasService } from '../shared/show-udas-checkbox/show-udas.service';
import { GridApi } from 'ag-grid-community';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'pup-work-item-list',
  templateUrl: './work-item-list.component.html',
  styleUrls: ['./work-item-list.component.css']
})
export class WorkItemListComponent implements OnInit {
  public workItems$;
  filter = WorkItemFilter.MyWorkItems;
  public gridPageSizeValue: number;
  pageDetail;
  public creationDateListField = BpmPiConstant.piListField.CREATEDTIME;
  public StateSelectorTypeEnum = StateSelectorType;

  public showUdas = false;
  searchFilter: BpmListCriteria = new BpmListCriteria();
  private columnDefsSingle: any[] = [{
    headerName: 'Id',
    field: 'id',
    sortable: true,
    filter: true
  },
  {
    headerName: 'Name',
    field: 'name',
    sortable: true,
    filter: true,
    tooltipValueGetter: () => 'Click to see more detail',
    cellRendererFramework: PupWorkItemNameColumnComponent
  },
  {
    headerName: 'Process',
    field: 'instanceName',
    sortable: true,
    filter: true,
    tooltipValueGetter: () => 'Click to see more detail',
    cellRendererFramework: PupWorkItemNameColumnComponent
  },
  {
    headerName: 'Assignee',
    field: 'assignee',
    sortable: true,
    filter: true
  },
  {
    headerName: 'Created',
    field: 'creationTime',
    sortable: true,
    filter: true,
    cellRenderer: (params) => this.datePipe.transform(params.value, 'yyyy/MM/dd')
  },
  {
    headerName: 'State',
    field: 'state',
    sortable: true,
    filter: true,
    cellRendererFramework: PupWorkItemStateComponent
  },
  {
    headerName: 'Priority',
    field: 'priority',
    sortable: true,
    filter: true,
    cellRendererFramework: PupPriorityTextComponent
  },
  {
    headerName: 'Due',
    field: 'creationTime',
    sortable: true,
    filter: true,
    cellRenderer: (params) => '<a >' + this.datePipe.transform(params.value, 'yyyy/MM/dd') + '</a>',
  }];
  columnDefs: any[] = this.columnDefsSingle;
  private columnDefsMulti: any[] = this.columnDefsSingle.concat([{
    headerName: 'Server',
    field: 'serverId',
    sortable: true,
    filter: true
  }]);

  private gridApi: GridApi;
  constructor(
    private bpmServerService: BpmServerService,
    private datePipe: DatePipe,
    private showUdasService: ShowUdasService,
  ) { }

  ngOnInit() {
    this.searchFilter.includeCount = true;
    this.searchFilter.batchSize = Number(localStorage.getItem('pageSize')) || environment.gridPageSize;
    if (this.bpmServerService.isMulti()) {
      console.log('Multi server');
      // Display application name in the grid.
      this.columnDefs = this.columnDefsMulti;
    }
    this.onFilter(this.searchFilter);
  }

  public onGridReady(params) {
    this.gridApi = params.api;
  }

  public refreshWorkItems(filter) {
    this.filter = filter;
    this.searchFilter.batchSize = this.gridPageSizeValue;
    this.searchFilter.batchOffset = 0;
    this.onFilter(this.searchFilter);
    // this.workItems$ = this.getWorkItems(this.filter);
  }

  updateGridPageSize(pageCount) {
    this.gridPageSizeValue = pageCount;
    localStorage.setItem('pageSize', this.gridPageSizeValue.toString());
    this.searchFilter.batchSize = this.gridPageSizeValue;
    this.searchFilter.batchOffset = 0;
    this.onFilter(this.searchFilter);
  }

  onFilter(searchFilter) {
    this.showUdasWhenNecessary(searchFilter);
    this.workItems$ = this.bpmServerService.getWorkItems(this.filter, searchFilter);
    this.workItems$.subscribe(result => {
      this.pageDetail = { totalItems: result.totalCount, pageSize: searchFilter.batchSize, filter: this.filter };
    })
  }

  private showUdasWhenNecessary(searchFilter) {
    if (!this.showUdas && this.showUdasService.hasUdaCriteria(searchFilter)) {
      this.showUdas = true;
      this.showUdasClicked();
    }
  }

  public showUdasClicked() {
    if (this.showUdas) {
      this.columnDefs.push(this.showUdasService.getUdasCellRenderer());
    } else {
      this.columnDefs.pop();
    }
    if (this.gridApi) {
      this.gridApi.setColumnDefs(this.columnDefs);
    }
  }

  public broadcastPageInfo(pageInfo) {
    this.searchFilter.batchOffset = pageInfo.pager.startIndex;
    this.onFilter(this.searchFilter);
  }

}
