import { Component, OnInit } from '@angular/core';
import { DxpApplicationConnection, MultiBpmConnectionService } from '../multi-bpm-connection.service';

@Component({
  selector: 'pup-multi-application-list',
  templateUrl: './multi-application-list.component.html',
  styleUrls: ['./multi-application-list.component.css']
})
export class MultiApplicationListComponent implements OnInit {
  public applications: DxpApplicationConnection[] = [];

  constructor(
    private multiBpmConnectionService: MultiBpmConnectionService
  ) { }

  ngOnInit() {
    this.applications = this.multiBpmConnectionService.getApplicationConnections();
  }

}
