import { Injectable } from '@angular/core';
import {
  BpmApplication, BpmConnectionApplication, BpmCreateInstance,
  BpmProcessDefinition, BpmProcessDefinitionDetail,
  BpmProcessInstance, BpmProcessInstanceDetail,
  BpmService,
  BpmWorkItem, BpmWorkItemDetail,
  ProcessDefinitionFilter,
  ProcessInstanceFilter,
  WorkItemFilter,
  BpmListCriteria
} from 'ng-bpm';
import { BpmConnectionService } from '../bpm-connection/bpm-connection.service';
import { Observable } from 'rxjs';
import {flatMap, tap} from 'rxjs/operators';
import { MultiBpmServerService } from '../../multi-server/multi-bpm-server.service';
import { BpmListResponse } from 'ng-bpm/lib/bo/bpm-list-response';

@Injectable({
  providedIn: 'root'
})
export class BpmServerService {

  constructor(
    private bpmService: BpmService,
    private bpmConnectionService: BpmConnectionService,
    private multiBpmServerService: MultiBpmServerService,
  ) { }

  public isMulti(): boolean {
    return this.multiBpmServerService.isMulti();
  }

  public getWorkItems(filter: WorkItemFilter, searchFilter: BpmListCriteria): Observable<any> {
    if (this.multiBpmServerService.isMulti()) {
      return this.multiBpmServerService.getWorkItems(filter, searchFilter);
    }

    return this.bpmConnectionService.getBpmConnectionApplication().pipe(
      flatMap(bpmConnectionApplication =>
        this.bpmService.getWorkItemListAdvanced(bpmConnectionApplication, filter, searchFilter))
    );
  }

  public getWorkItemDetailById(bpmConnectionApplication: BpmConnectionApplication,
                               workItemId: number): Observable<BpmWorkItemDetail> {
    return this.bpmService.getWorkItemDetailById(bpmConnectionApplication, workItemId);
  }

  public getWorkItemDetail(wi: BpmWorkItem): Observable<BpmWorkItemDetail> {
    return this.bpmService.getWorkItemDetail(wi);
  }

  public accept(wi: BpmWorkItem): Observable<BpmWorkItemDetail> {
    return this.bpmService.accept(wi);
  }

  public decline(wi: BpmWorkItem): Observable<BpmWorkItemDetail> {
    return this.bpmService.decline(wi);
  }


  public getProcessInstances(filter: ProcessInstanceFilter, searchFilter: BpmListCriteria): Observable<any> {
    if (this.multiBpmServerService.isMulti()) {
      return this.multiBpmServerService.getProcessInstances(filter, searchFilter);
    }

    return this.bpmConnectionService.getBpmConnectionApplication().pipe(
      flatMap(bpmConnectionApplication =>
        this.bpmService.getProcessInstanceListAdvanced(bpmConnectionApplication, filter, searchFilter))
    );
  }

  public getProcessInstanceDetailById(bpmConnectionApplication: BpmConnectionApplication,
                                      processInstanceId: number): Observable<BpmProcessInstanceDetail> {
    return this.bpmService.getProcessInstanceDetailById(bpmConnectionApplication, processInstanceId);
  }

  public getProcessInstanceDetail(pi: BpmProcessInstance): Observable<BpmProcessInstanceDetail> {
    return this.bpmService.getProcessInstanceDetail(pi);
  }

  public createProcessInstance(bpmCreateInstance: BpmCreateInstance): Observable<BpmProcessInstance> {
    return this.bpmService.createInstance(bpmCreateInstance);
  }

  public suspend(pi: BpmProcessInstance): Observable<BpmProcessInstanceDetail> {
    return this.bpmService.suspend(pi);
  }

  public resume(pi: BpmProcessInstance): Observable<BpmProcessInstanceDetail> {
    return this.bpmService.resume(pi);
  }

  public abort(pi: BpmProcessInstance): Observable<BpmProcessInstanceDetail> {
    return this.bpmService.abort(pi);
  }

  public getProcessInstanceHistory(pi: BpmProcessInstance): Observable<any[]> {
    return this.bpmService.getProcessInstanceHistory(pi);
  }

  public getProcessDefinitions(filter: ProcessDefinitionFilter, searchFilter: BpmListCriteria): Observable<any> {
    if (this.multiBpmServerService.isMulti()) {
      return this.multiBpmServerService.getProcessDefinitions(filter);
    }

    return this.bpmConnectionService.getBpmConnectionApplication().pipe(
      flatMap(bpmConnectionApplication =>
        this.bpmService.getProcessDefinitionListAdvanced(bpmConnectionApplication, filter, searchFilter))
    );
  }

  public getProcessDefinitionDetailById(bpmConnectionApplication: BpmConnectionApplication,
                                        processDefinitionId: number): Observable<BpmProcessDefinitionDetail> {
    return this.bpmService.getProcessDefinitionDetailById(bpmConnectionApplication, processDefinitionId);
  }

  public getProcessDefinitionDetail(pd: BpmProcessDefinition): Observable<BpmProcessDefinitionDetail> {
    return this.bpmService.getProcessDefinitionDetail(pd);
  }

  public getApplication(): Observable<BpmApplication> {
    return this.bpmConnectionService.getBpmConnectionApplication().pipe(
      flatMap(bpmConnectionApplication =>
        this.bpmService.getApplication(bpmConnectionApplication))
    );
  }

}
