import { Component, EventEmitter, Input, Output } from '@angular/core';
import { BpmService, BpmWorkItemDetail } from 'ng-bpm';

@Component({
  selector: 'pup-work-item-choices-buttons',
  templateUrl: './work-item-choices-buttons.component.html',
  styleUrls: ['./work-item-choices-buttons.component.css']
})
export class WorkItemChoicesButtonsComponent {
  @Input() workItem: BpmWorkItemDetail;
  @Output() workItemChange = new EventEmitter<BpmWorkItemDetail>();
  @Output() choiceMade = new EventEmitter<string>();

  constructor(
    private bpmService: BpmService,
  ) {}

  choiceClicked(choiceName: string) {
    this.bpmService.makeChoice(this.workItem, choiceName)
      .subscribe((result: BpmWorkItemDetail) => {
        this.workItem = result;
        this.choiceMade.emit(choiceName);
      });
  }

  saveClicked() {
    console.log('saveClicked', this.workItem);
    this.bpmService.saveWorkItemUDAs(this.workItem)
      .subscribe((workItem: BpmWorkItemDetail) => {
        this.workItem = workItem;
        this.workItemChange.emit(this.workItem);
      });
  }
}
