import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { Observable } from 'rxjs';
import {
  BpmCreateInstance,
  BpmProcessDefinitionDetail,
  DataItemRef
} from 'ng-bpm';
import { MultiBpmServerService } from '../multi-server/multi-bpm-server.service';
import { ActivatedRoute, Router } from '@angular/router';
import { flatMap, tap } from 'rxjs/operators';
import { BpmConnectionService } from '../shared/bpm-connection/bpm-connection.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { BpmServerService } from '../shared/bpm-server/bpm-server.service';

@Component({
  selector: 'pup-process-definition-detail',
  templateUrl: './process-definition-detail.component.html',
  styleUrls: ['./process-definition-detail.component.css']
})
export class ProcessDefinitionDetailComponent implements OnInit {
  @ViewChild('content') el: ElementRef;
  public processDefinitionDetail$: Observable<BpmProcessDefinitionDetail>;
  public processDefinitionId: number;
  public udaValues;
  public disableStartButton = false;

  // This is used only in the component and not the template
  private myProcessDefinitionDetail: BpmProcessDefinitionDetail;

  constructor(
    private bpmConnectionService: BpmConnectionService,
    private route: ActivatedRoute,
    private multiBpmServerService: MultiBpmServerService,
    private bpmServerService: BpmServerService,
    private modalService: NgbModal,
    private router: Router
  ) { }

  ngOnInit() {
    this.processDefinitionDetail$ = this.getProcessDefinitionDetail();
  }

  private getProcessDefinitionDetail(): Observable<BpmProcessDefinitionDetail> {
    if (this.multiBpmServerService.isMulti()) {
      return this.getProcessDefinitionDetailMulti();
    }
    this.processDefinitionId = +this.route.snapshot.paramMap.get('pdId');
    return this.getProcessDefinitionDetailSingle(this.processDefinitionId);
  }

  private getProcessDefinitionDetailSingle(processDefinitionId: number): Observable<BpmProcessDefinitionDetail> {
    return this.bpmConnectionService.getBpmConnectionApplication().pipe(
      flatMap(bpmConnectionApplication =>
        this.bpmServerService.getProcessDefinitionDetailById(bpmConnectionApplication, processDefinitionId).pipe(
          tap((processDefinitionDetail: BpmProcessDefinitionDetail) => this.refreshProcessDefinitionDetail(processDefinitionDetail))
        )
      )
    );
  }

  // in multi-server the ID of the Process Definition is not enough to identify it
  // So in multi server, the Process Definition List will have stored the selected Process Definition
  private getProcessDefinitionDetailMulti(): Observable<BpmProcessDefinitionDetail> {
    return this.bpmServerService.getProcessDefinitionDetail(this.multiBpmServerService.selectedProcessDefinition).pipe(
      tap((processDefinitionDetail: BpmProcessDefinitionDetail) => this.refreshProcessDefinitionDetail(processDefinitionDetail))
    );
  }

  private refreshProcessDefinitionDetail(processDefinitionDetail: BpmProcessDefinitionDetail) {
    this.myProcessDefinitionDetail = processDefinitionDetail;
    this.populateUdaDefaultValues(processDefinitionDetail);
  }

  private isNumberType(dataItemRef: DataItemRef): boolean {
    return (dataItemRef.type === 'INTEGER' || dataItemRef.type === 'DATE' || dataItemRef.type === 'BIGDECIMAL' ||
      dataItemRef.type === 'FLOAT' || dataItemRef.type === 'LONG');
  }

  private populateUdaDefaultValues(processDefinitionDetail: BpmProcessDefinitionDetail) {
    this.udaValues = {};
    processDefinitionDetail.dataItemRefs.forEach(
      (dataItemRef) => {
        // Set the value based on the type of the UDA
        this.udaValues[dataItemRef.identifier] = dataItemRef.initValue;
        if (this.isNumberType (dataItemRef)) {
          this.udaValues[dataItemRef.identifier] = Number(dataItemRef.initValue);
        }
        if (dataItemRef.type === 'BOOLEAN') {
          this.udaValues[dataItemRef.identifier] = Boolean(dataItemRef.initValue);
        }
      }
    );
  }

  public startClicked() {
    this.disableStartButton = true;
    const bpmCreateInstance = BpmCreateInstance.createProcessFromProcessDefinition(this.myProcessDefinitionDetail);
    bpmCreateInstance.uda = this.udaValues;
    this.bpmServerService.createProcessInstance(bpmCreateInstance)
      .subscribe(() => {
        this.disableStartButton = false;
        this.modalService.open(this.el);
        // processInstanceLaunched();
      });
  }

  backToNewProcess() {
    this.router.navigate(['/newProcess'], { queryParamsHandling: 'merge' });
  }

}
