import { Component, Input, Output, EventEmitter, OnChanges, SimpleChanges, OnInit } from '@angular/core';
import { BpmProcessInstance, BpmWorkItem, WorkItemFilter } from 'ng-bpm';
import { Router } from '@angular/router';
import { MultiBpmServerService } from '../../multi-server/multi-bpm-server.service';
import {faInfo} from "@fortawesome/free-solid-svg-icons";


// This is used by ag-grid to be able to display the Work Item state.
@Component({
    selector: 'pup-work-item-state',
    template: `
        <bpm-work-item-state [state]="params.value"></bpm-work-item-state>
    `
})
export class PupWorkItemStateComponent {
  public params;

  agInit(params): void {
    this.params = params;
  }
}


@Component({
    template: `
    <a class="text-primary" (click)="renderPage(params)">{{params.value}}</a>
    `
})
export class PupWorkItemNameColumnComponent {
    public params;

    // Monitor if process instance state comes to be 1 or 2 as pup-angular-ui.js have those value too.
    constructor(
      private router: Router,
      private multiBpmServerService: MultiBpmServerService,
    ) {}

    agInit(params): void {
      this.params = params;
    }

    renderPage(params: any) {
      const clickedWorkItem: BpmWorkItem = params.data;
      if (params.colDef.field === 'name') {
        if (this.multiBpmServerService.isMulti()) {
          // in multi-server the ID of the Work Item is not enough to identify it
          // So in multi server, the Work List must store the selected work item
          this.multiBpmServerService.selectedWorkItem = clickedWorkItem;
        }
        this.router.navigate([ '/wi/' + clickedWorkItem.id ], { queryParamsHandling: 'merge' });
      } else if (params.colDef.field === 'instanceName') {
        if (this.multiBpmServerService.isMulti()) {
          // in multi-server the ID of the Process Instance is not enough to identify it
          // So in multi server, the Process List must store the selected process Instance
          const bpmProcessInstance = new BpmProcessInstance();
          bpmProcessInstance.applicationConnection = clickedWorkItem.applicationConnection;
          bpmProcessInstance.idInst = clickedWorkItem.instanceId;
          this.multiBpmServerService.selectedProcessInstance = bpmProcessInstance;
        }
        this.router.navigate([ '/pi/' + clickedWorkItem.instanceId ], { queryParamsHandling: 'merge' });
      }
    }
}


