import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, NavigationEnd, Router, RouterEvent } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { faBars } from '@fortawesome/free-solid-svg-icons';
import { filter, map, mergeMap } from 'rxjs/operators';


export enum NavigationTabs {
  Home,
  NewProcess,
  Tasks,
  Processes,
  Dashboard,
}

@Component({
  selector: 'pup-navigation-bar',
  templateUrl: './navigation-bar.component.html',
  styleUrls: ['./navigation-bar.component.css']
})
export class NavigationBarComponent implements OnInit {
  public isCollapsed = true;
  public selectedTab: NavigationTabs;
  public selectedLanguage = 'en';
  public showLanguageSelectList = true;

  public faBars = faBars;
  public NavigationTabsEnum = NavigationTabs;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private translate: TranslateService
  ) { }

  ngOnInit() {
    this.setSelectedTabFromRoute();
  }

  private getDeepestActivatedRoute(): ActivatedRoute {
    let myRoute = this.route;
    while (myRoute.firstChild) {
      myRoute = myRoute.firstChild;
    }
    return myRoute;
  }

  private setSelectedTabFromRoute() {
    this.router.events.pipe(
      filter((event: RouterEvent) => event instanceof NavigationEnd),
      map(() => this.getDeepestActivatedRoute()),
      filter((route: ActivatedRoute) => route.outlet === 'primary'),
      mergeMap((route: ActivatedRoute) => route.data)
    ).subscribe((routeData: {tab: NavigationTabs}) => {
      this.selectedTab = routeData.tab;
    });
  }

  public async selectLanguage(selectedLanguage) {
    try {
      this.translate.use(selectedLanguage);
    } catch (error) {
    }

  }


  public async navigateToMenu(navigate) {
    try {
      this.router.navigate([navigate], { queryParamsHandling: 'merge' });
      window.scrollTo(0, 0);
    } catch (error) {

    }
  }


}
