import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { BpmFilterByListField } from 'ng-bpm';
import { FilterStateService, StateSelectorType } from './filter-state.service';

@Component({
  selector: 'pup-filter-state-selector',
  templateUrl: './filter-state-selector.component.html',
  styleUrls: ['./filter-state-selector.component.css']
})
export class FilterStateSelectorComponent implements OnInit {
  @Input() stateType: StateSelectorType;
  @Output() filtersChange = new EventEmitter<BpmFilterByListField>();

  public states: string[];
  public stateLabels: string;
  public stateValues = {};

  constructor(
    private filterStateService: FilterStateService
  ) { }

  ngOnInit() {
    if (this.stateType === StateSelectorType.PROCESS_INSTANCE) {
      this.states = ['0', '7', '8', '10', '11', '12'];
      this.stateLabels = 'PROCESSINSTANCE_STATE';
    }
    if (this.stateType === StateSelectorType.WORK_ITEM) {
      this.states = ['-1', '3', '4', '6', '8', '12', '14', '15', '16', '25'];
      this.stateLabels = 'WORK_ITEM_STATE';
    }

    this.states.forEach((state: string) => this.stateValues[state] = false);
  }

  public stateClicked() {
    this.filtersChange.emit(this.filterStateService.getStateFilter(this.stateValues, this.stateType));
  }

}
