import { Injectable } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { Observable } from 'rxjs';
import { BpmConnectionApplication, BpmConnectionServer, BpmConnectionTenant } from 'ng-bpm';
import { map } from 'rxjs/operators';
import { BaseUrlService } from '../base-url/base-url.service';

@Injectable({
  providedIn: 'root'
})
export class BpmConnectionService {
  constructor(
    private route: ActivatedRoute,
    private baseUrlService: BaseUrlService,
  ) { }

  private getParams(): Observable<Params> {
    return this.route.queryParams;
  }

  private getUrlSlice(hrefUrl: string, sliceLabel: string): string {
    // 01234567890123456789012345678901234567890123456789
    // http://dxpdemo198:49950/aa/api/t=Default/a=Test/ui/index.html#/
    const beginIndex = hrefUrl.indexOf(sliceLabel);
    const startTenantIndex = beginIndex + sliceLabel.length;
    const endTenantIndex = hrefUrl.indexOf('/', startTenantIndex);
    return hrefUrl.slice(startTenantIndex, endTenantIndex);
  }

  private getTenantFromUrl(): string {
    return  this.getUrlSlice(window.location.href, '/t=');
  }

  private getApplicationFromUrl(): string {
    return  this.getUrlSlice(window.location.href, '/a=');
  }

  // sometimes PUP may be called as a custom application so we need to get the tenant and app from the url instead of the params
  // For example PUP can be invoked in two ways:
  // http://dxpdemo198:49950/aa/api/t=Default/a=Test/ui/index.html#/
  private verifyTenantAndApplication(params: Params) {
    if (!params.t || !params.a) {
      // PUP is being called as a custom application so get the tenant and app from the url
      // the URL looks like this
      // http://dxpdemo198:49950/aa/api/t=Default/a=Test/ui/index.html#/
      params.t = this.getTenantFromUrl();
      params.a = this.getApplicationFromUrl();
    }
    return params;
  }

  public getTenantAndApplication(): Observable<Params> {
    return this.getParams().pipe(
      map(params => this.verifyTenantAndApplication(params)),
    );
  }

  public getBpmConnectionApplication(): Observable<BpmConnectionApplication> {
    return this.getTenantAndApplication().pipe(
      map(params => new BpmConnectionApplication(this.baseUrlService.baseUrl, params.t, params.a))
    );
  }

  public getBpmConnectionTenant(): Observable<BpmConnectionTenant> {
    return this.getTenantAndApplication().pipe(
      map(params => new BpmConnectionTenant(this.baseUrlService.baseUrl, params.t))
    );
  }

  public getBpmConnectionServer(): Observable<BpmConnectionServer> {
    return this.getTenantAndApplication().pipe(
      map(params => new BpmConnectionServer(this.baseUrlService.baseUrl))
    );
  }

}
