import { Component, EventEmitter, Output } from '@angular/core';
import { NgbDate } from '@ng-bootstrap/ng-bootstrap';
import { DateRange, DatesService } from '../dates.service';
import { faCalendarAlt} from '@fortawesome/free-solid-svg-icons';

@Component({
  selector: 'pup-datepicker-range',
  templateUrl: './datepicker-range.component.html',
  styleUrls: ['./datepicker-range.component.css']
})
export class DatepickerRangeComponent {
  @Output() dateRangeChange = new EventEmitter<DateRange>();

  public faCalendarAlt = faCalendarAlt;

  constructor(
    private datesService: DatesService
  ) {}

  public dateRange = {from: undefined, to: undefined};
  public showDatePicker = false;

  hoveredDate: NgbDate;

  onDateSelection(date: NgbDate) {
    if (!this.dateRange.from && !this.dateRange.to) {
      this.dateRange.from = date;
    } else if (this.dateRange.from && !this.dateRange.to && date.after(this.dateRange.from)) {
      this.dateRange.to = date;
    } else {
      this.dateRange.to = null;
      this.dateRange.from = date;
    }
    if (this.dateRange.from && this.dateRange.to) {
      this.showDatePicker = false;
    }
    this.dateRangeChange.emit(this.dateRange);
  }

  isHovered(date: NgbDate) {
    return this.dateRange.from && !this.dateRange.to && this.hoveredDate &&
      date.after(this.dateRange.from) && date.before(this.hoveredDate);
  }

  public isInside(date: NgbDate): boolean {
    return this.datesService.isInside(date, this.dateRange);
  }

  isRange(date: NgbDate) {
    return date.equals(this.dateRange.from) || date.equals(this.dateRange.to) || this.isInside(date) || this.isHovered(date);
  }

  public dateRangeClick() {
    this.showDatePicker = true;
  }

  public buttonClick(dateRange: DateRange) {
    this.dateRange = dateRange;
    this.showDatePicker = false;
    this.dateRangeChange.emit(this.dateRange);
  }

  public dateFormat(date: NgbDate): string {
    if (date) {
      return `${date.year}/${date.month}/${date.day}`;
    }
    return 'yyyy/mm/dd';
  }
}
