import {Component, Input, OnChanges, OnInit} from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'pup-error-display',
  templateUrl: './error-display.component.html',
  styleUrls: ['./error-display.component.css']
})
export class ErrorDisplayComponent implements OnChanges, OnInit {
  @Input() public error: any;

  constructor(
    public activeModal: NgbActiveModal
  ) { }

  errorMsgs = [];
  errorTrace = [];
  showTrace = false;

  ngOnInit() {
    this.ngOnChanges();
  }

  ngOnChanges() {
    try {
      this.error.error = this.isJson(this.error.error);
      this.errorMsgs = this.error.error.error.details;
    this.errorTrace = this.error.error.error.stack;
   } catch(e) {
    console.log('Error in rest call');
   }
   
  }

  onClose(): void {
    this.activeModal.close('closed');
  }

  isJson(str) {
    try {
      return JSON.parse(str);
    } catch (e) {
      return str;
    }
    // return true;
}

  onDismiss(reason: string): void {
    this.activeModal.dismiss(reason);
  }

}
