import { Injectable } from '@angular/core';
import { BpmConnectionApplication } from 'ng-bpm';
import multi from '../../assets/multi.json';

export interface DxpApplicationConnection {
  id: string;
  app: BpmConnectionApplication;
}

@Injectable({
  providedIn: 'root'
})
export class MultiBpmConnectionService {

  constructor() {
    this.usingMultiServer = multi.servers && multi.servers.length > 0;
  }

  readonly usingMultiServer;

  public isMulti(): boolean {
    return this.usingMultiServer;
  }

  public getApplicationConnections(): DxpApplicationConnection[] {
    return multi.servers.map(this.dxpApplicationConnectionFactory);
  }

  // The connection info for an application looks like this:
  // id: 'Testing',
  // url: 'http://dxpdemo7:49950/aa',
  // tenant: 'Default',
  // app: 'Testing'
  private dxpApplicationConnectionFactory(server: any): DxpApplicationConnection {
    return {
      id: server.id,
      app: new BpmConnectionApplication(server.url, server.tenant, server.app)
    };
  }

}
