import { Component, EventEmitter, Output} from '@angular/core';
import { DateRange, DatesService } from '../dates.service';

@Component({
  selector: 'pup-date-range-buttons',
  templateUrl: './date-range-buttons.component.html',
  styleUrls: ['./date-range-buttons.component.css']
})
export class DateRangeButtonsComponent {
  @Output() rangeClick = new EventEmitter<DateRange>();

  constructor(
    private datesService: DatesService
  ) {}

  public lastMonth() {
    this.rangeClick.emit(this.datesService.lastMonth());
  }

  public lastWeek() {
    this.rangeClick.emit(this.datesService.lastWeek());
  }

  public yesterday() {
    this.rangeClick.emit(this.datesService.yesterday());
  }

}
