import { Injectable } from '@angular/core';
import {
  BpmProcessDefinition,
  BpmProcessInstance,
  BpmService,
  BpmWorkItem, ProcessDefinitionFilter,
  ProcessInstanceFilter,
  WorkItemFilter
} from 'ng-bpm';
import { merge, Observable, of } from 'rxjs';
import { DxpApplicationConnection, MultiBpmConnectionService } from './multi-bpm-connection.service';
import {catchError, map, tap} from 'rxjs/operators';


@Injectable({
  providedIn: 'root'
})
export class MultiBpmServerService {
  public selectedWorkItem: BpmWorkItem;
  public selectedProcessInstance: BpmProcessInstance;
  public selectedProcessDefinition: BpmProcessDefinition;

  constructor(
    private bpmService: BpmService,
    private multiBpmConnectionService: MultiBpmConnectionService,
  ) { }

  public isMulti(): boolean {
    return this.multiBpmConnectionService.isMulti();
  }

  public getWorkItems(filter: WorkItemFilter, searchFilter?: any): Observable<BpmWorkItem[]> {
    // Empty list of work items that will be constantly updated as we get results from servers.
    let workItems: BpmWorkItem[] = [];

    // Get a list a connection objects, one for each application.
    const dxpApplicationConnections: DxpApplicationConnection[] = this.multiBpmConnectionService.getApplicationConnections();

    // Change the list of connections into a list of observables.
    // Each observable resolves to an array of WIs from an individual application
    const observables = dxpApplicationConnections.map((dxpApplicationConnection: DxpApplicationConnection) =>
      this.bpmService.getWorkItemList(dxpApplicationConnection.app, filter, searchFilter).pipe(
        catchError(err => {
          console.error('Error fetching from server', err, dxpApplicationConnection);
          return of([]);
        }),
        tap( (workItemList: BpmWorkItem[]) =>
          // We cast the BpmWorkItem to any so we can add a reference to the server id for display in the list.
          workItemList.map( (workItem: any) => workItem.serverId = dxpApplicationConnection.id)
        ),
      )
    );

    // merge manages the subscribes to each observable and makes the array into a single observable
    // notice we use the spread operator ... to split the array into arguments.
    // Then we return the main merged observable.
    // Notice nothing actually happens until someone subscribes to the one main observable.
    return merge(...observables).pipe(
      // this concats the WIs from each server to the Worklist.
      map( (wis: BpmWorkItem[]) => workItems = workItems.concat(wis)),
    );
  }

  public getProcessInstances(filter: ProcessInstanceFilter, searchFilter?: any): Observable<BpmProcessInstance[]> {
    let processInstances: BpmProcessInstance[] = [];

    const dxpApplicationConnections: DxpApplicationConnection[] = this.multiBpmConnectionService.getApplicationConnections();

    const observables = dxpApplicationConnections.map( (dxpApplicationConnection: DxpApplicationConnection) =>
      this.bpmService.getProcessInstanceList(dxpApplicationConnection.app, filter, searchFilter).pipe(
        catchError(err => {
          console.error('Error fetching from server', err, dxpApplicationConnection);
          return of([]);
        }),
        tap( (processInstanceList: BpmProcessInstance[]) =>
          // We cast this to any so we can add a reference to the server id for display in the list.
          processInstanceList.map( (processInstance: any) => processInstance.serverId = dxpApplicationConnection.id)
        ),
      )
    );

    return merge(...observables).pipe(
      // this concats the WIs from each server to the Worklist.
      map( (pis: BpmProcessInstance[]) => processInstances = processInstances.concat(pis)),
    );
  }

  public getProcessDefinitions(filter: ProcessDefinitionFilter): Observable<BpmProcessDefinition[]> {
    let processDefinitions: BpmProcessDefinition[] = [];

    const dxpApplicationConnections: DxpApplicationConnection[] = this.multiBpmConnectionService.getApplicationConnections();

    const observables = dxpApplicationConnections.map( (dxpApplicationConnection: DxpApplicationConnection) =>
      this.bpmService.getProcessDefinitionList(dxpApplicationConnection.app, filter).pipe(
        catchError(err => {
          console.error('Error fetching from server', err, dxpApplicationConnection);
          return of([]);
        }),
        tap( (processDefinitionList: BpmProcessDefinition[]) =>
          // We cast this to any so we can add a reference to the server id for display in the list.
          processDefinitionList.map( (processDefinition: any) => processDefinition.serverId = dxpApplicationConnection.id)
        ),
      )
    );

    return merge(...observables).pipe(
      // this concats the PDs from each server to the list.
      map( (pis: BpmProcessDefinition[]) => processDefinitions = processDefinitions.concat(pis)),
    );
  }

}
