// This component is used by ag-grid to display the process instance name.
@Component({
  template: `<a class="text-primary" (click)="renderPage(params)">{{params.value}}</a>`
})
export class PupProcessRendererComponent  {
  public params: any;

  constructor(
    private router: Router,
    private multiBpmServerService: MultiBpmServerService,
  ) {}

  agInit(params: any): void {
      this.params = params;
  }

  renderPage(params: any) {
    const clickedProcessInstance: BpmProcessInstance = params.data;
    if (this.multiBpmServerService.isMulti()) {
      // in multi-server the ID of the Work Item is not enough to identify it
      // So in multi server, the Work List must store the selected work item
      this.multiBpmServerService.selectedProcessInstance = clickedProcessInstance;
    }
    this.router.navigate([ '/pi/' + clickedProcessInstance.idInst ], { queryParamsHandling: 'merge' });
  }
}


@Component({
    selector: 'pup-process-instance-graph',
    template: `
    <div *ngIf="reqStatus == 200" [innerHTML]="svg"></div>
    <ul class="alert alert-warning-static pl-5 mb-2" *ngIf="reqStatus == 400">
    <li>{{'esDownMsg' | translate}}</li>
    <li *ngFor="let msg of errorMsgs">{{msg.message}}</li>
    </ul>
    `
})
export class PupProcessInstanceGraphComponent implements OnChanges {
    @Input() piid: any;
    @Input() processInstance: any;
    svg;
    reqStatus;
    errorMsgs = [];
    constructor(
      private sanitizer: DomSanitizer,
      private bpmService: BpmService,
      private restGenericService: RestGenericService,
      private modalService: NgbModal
    ) {}

    ngOnChanges(changes: SimpleChanges): void {
        this.svg = '';
        this.errorMsgs = [];
        if (this.piid) {
            try {
                this.restGenericService.httpGet(this.bpmService.getProcessInstanceGraphUrl(this.processInstance))
                .subscribe( (data) => {
                    this.reqStatus = 200;
                    this.svg = this.sanitizer.bypassSecurityTrustHtml(data);
                },
                error => {
                  this.modalService.dismissAll();
                  error.error = JSON.parse(error.error);
                  this.reqStatus = error.status;
                  if (error.error) {
                     this.errorMsgs = error.error.error.details;
                  }
                  console.error('unable to get the ready file:', this.errorMsgs, error.status);
                });
            } catch (error) {
                this.modalService.dismissAll();
                // this.reqStatus = status;
                if (error.error) {
                   this.errorMsgs = error.error.error.details;
                }
                //    console.log("unable to get the ready file:", error, status);
            }
        }
    }
}
