import {Component, EventEmitter, Input, Output, ViewChild} from '@angular/core';
import {
  BpmFilterByListField,
  BpmFilterByUda,
  BpmListCriteriaJson,
  BpmSortByUda,
  BpmSqlOperator,
  BpmUdaType,
  BpmListCriteria
} from 'ng-bpm';
import { StateSelectorType } from '../filter-state-selector/filter-state.service';
import { FilterUdaComponent } from '../filter-uda/filter-uda.component';
import { SortUdaComponent } from '../sort-uda/sort-uda.component';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'pup-list-filter',
  templateUrl: './list-filter.component.html',
  styleUrls: ['./list-filter.component.css']
})
export class ListFilterComponent {
  @Input() stateType: StateSelectorType;
  @Input() creationDateListField: number;
  @Output() searchClick = new EventEmitter<any>();

  @ViewChild(FilterUdaComponent) filterUdaComponent: FilterUdaComponent;
  @ViewChild(SortUdaComponent) sortUdaComponent: SortUdaComponent;

  public isShowFilter;

  protected searchFilter: BpmListCriteria = new BpmListCriteria();

  private stateFilter: BpmFilterByListField = undefined;
  private creationDateFilterList: BpmFilterByListField[] = [];
  public udaFilterList: BpmFilterByUda[] = [];
  public udaSortList: BpmSortByUda[] = [];
  constructor() {
    this.searchFilter.sortBy = [];
    this.searchFilter.includeCount = true;
    this.searchFilter.batchSize = Number(localStorage.getItem('pageSize')) || environment.gridPageSize;
  }
  showFilter(flag) {
    this.isShowFilter = flag;
  }

  public dateFilterChange(dateFilters: BpmFilterByListField[]) {
    this.creationDateFilterList = dateFilters;
  }

  public stateFilterChange(stateFilter: BpmFilterByListField) {
    this.stateFilter = stateFilter;
  }

  public udaFilterChange(udaFilters: BpmFilterByUda[]) {
    console.log('udaFilterChange', udaFilters);
    this.udaFilterList = udaFilters;
  }

  public udaSortChange(udaSortBys: BpmSortByUda[]) {
    console.log('udaSortChange', udaSortBys);
    this.udaSortList = udaSortBys;
  }

  public applyFilter() {
    this.searchFilter.filterBy = this.creationDateFilterList;
    this.searchFilter.filterBy = this.searchFilter.filterBy.concat(this.udaFilterList);
    this.searchFilter.sortBy = this.searchFilter.sortBy.concat(this.udaSortList);
    if (this.stateFilter) {
      this.searchFilter.filterBy.push(this.stateFilter);
    }
    this.searchClick.emit(this.searchFilter);
  }

  public addUdaFilter() {
    this.filterUdaComponent.addUdaFilter();
  }

  public addUdaSort() {
    this.sortUdaComponent.addUdaSort();
  }

}
