import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import moment from 'moment';
import { NgbDate} from '@ng-bootstrap/ng-bootstrap';
import {faPencilAlt} from '@fortawesome/free-solid-svg-icons';

@Component({
  selector: 'pup-dashboard-search-filter',
  templateUrl: './dashboard-search-filter.component.html',
  styleUrls: ['./dashboard-search-filter.component.css']
})
export class DashboardSearchFilterComponent implements OnInit {
  dateFilterFlag = false;
  @Input()  isModifySearch = false;
  searchQuery;
  @Input() defaultRefreshInterval;
  @Input() defaultDateRange;
  @Input() paneledit;
  @Input() query;
  @Input()  showDaterange;
  @Output() cancelPanel = new EventEmitter<any>();
  @Output() broadcastDate = new EventEmitter<any>();
  @Output() editPanelListener = new EventEmitter<any>();
  firstDate;
  startDate;
  selectdateRange: { startDate: string; startDay: string; endDate: string; endDay: string;};
  selectedRefreshIntervalValue;
  selectedRefreshIntervalOption;
  endDate;
  public faPencilAlt = faPencilAlt;

  refreshIntervals = [
    {name: 'Off', value: 0},
    {name: '5 Seconds', value: 5},
    {name: '10 Seconds', value: 10},
    {name: '30 Seconds', value: 30},
    {name: '45 Seconds', value: 45},
    {name: '1 Minute', value: 60},
    {name: '15 Minutes', value: 900},
    {name: '30 Minutes', value: 1800}
  ];

  selectedQuery;

  toggelDateFilter() {
    if (this.dateFilterFlag) {
      this.dateFilterFlag = false;
    } else {
      this.dateFilterFlag = true;
    }
  }

  ngOnInit() {
    this.selectedQuery = this.query;
    this.searchQuery = this.selectedQuery;
    this.firstDate = this.defaultDateRange;
    this.selectdateRange = this.covertDateFormat(this.defaultDateRange);
    this.selectedRefreshIntervalValue = this.defaultRefreshInterval;

    // TODO: This uses break. So we are only searching for one item. Shouldn't we use filter or find?
    // tslint:disable-next-line: prefer-for-of
    for (let i = 0; i < this.refreshIntervals.length; i++) {
      if (this.refreshIntervals[i].value === this.selectedRefreshIntervalValue) {
        this.selectedRefreshIntervalOption = this.refreshIntervals[i];
        break;
      }
    }
  }

  searchDateRange() {
    if (this.searchQuery === '') {
      this.searchQuery = '*';
    }
    this.selectedQuery = this.searchQuery;
    this.selectdateRange = this.covertDateFormat(this.firstDate);
    this.selectedRefreshIntervalValue = this.selectedRefreshIntervalOption.value || 0;
    this.isModifySearch = false;
    // tslint:disable-next-line: max-line-length
    this.broadcastDate.emit({arg1: { dateRange: this.firstDate }, arg2: { query: this.searchQuery }, arg3: {value: this.selectedRefreshIntervalValue}});
  }

  getRefreshIntervalName(value) {
    // tslint:disable-next-line: prefer-for-of
    for (let i = 0; i < this.refreshIntervals.length; i++) {
      if (this.refreshIntervals[i].value === value) {
        return this.refreshIntervals[i].name;
      }
    }
    return null;
  }

  modifySearch() {
    this.isModifySearch = true;
    if (this.paneledit) {
      this.cancelPanel.emit();
    }
  }

  canselSearch() {
    this.isModifySearch = false;
  }

  covertDateFormat(date) {
    if (date !== undefined) {
      this.selectdateRange = {
        startDate: moment(date.startDate).format('MM/DD'),
        startDay: moment(date.startDate).format('dd'),
        endDate: moment(date.endDate).format('MM/DD'),
        endDay: moment(date.endDate).format('dd')
      };
    }
    return this.selectdateRange;
  }

  public dateRangeChange(dateRange: {from: NgbDate, to: NgbDate}) {
    if (dateRange.from && dateRange.to) {
      dateRange = JSON.parse(JSON.stringify(dateRange));
      // Months in moment are 0 based, months in ng-bootstrap are 1 based.
      dateRange.from.month--;
      dateRange.to.month--;
      this.firstDate = {
        startDate: moment(dateRange.from),
        startDay: moment(dateRange.from),
        endDate: moment(dateRange.to),
        endDay: moment(dateRange.to)
      };
    }
  }

  simpleDateFormat(date) {
    return moment(date).format('DD-MM-YYYY');
  }

  editPanel() {
    this.editPanelListener.emit({ flag: true });
  }


}
