import {Component, EventEmitter, Input, Output} from '@angular/core';
import { Router } from '@angular/router';
import { MultiBpmServerService } from '../../multi-server/multi-bpm-server.service';
import { BpmProcessDefinition } from 'ng-bpm';

// This wrapper component is necessary for ag-grid
@Component({
  selector: 'pup-process-definition-state',
  template: `
        <span *ngIf="params">
            <bpm-process-definition-state [state]="params.value"></bpm-process-definition-state>
        </span>
    `
})
export class PupProcessDefinitionStateComponent {
  public params;

  agInit(params): void {
    this.params = params;
  }
}

// This component is for ag-grid to display the name of the process definition.
// It doesn't need a selector because it is passed as a class to ag-grid.
@Component({
  template: `
      <a class="text-primary" (click)="renderPage(params)">{{params.value}}</a>
  `
})
export class PupProcessDefinitionNameColumnComponent {
  public params;

  constructor(
    private router: Router,
    private multiBpmServerService: MultiBpmServerService,
  ) {}

  agInit(params): void {
    this.params = params;
  }

  renderPage(params: any) {
      const clickedProcessDefinition: BpmProcessDefinition = params.data;
      if (this.multiBpmServerService.isMulti()) {
        // in multi-server the ID of the Work Item is not enough to identify it
        // So in multi server, the Work List must store the selected work item
        this.multiBpmServerService.selectedProcessDefinition = clickedProcessDefinition;
      }
      this.router.navigate([ '/pd/' + clickedProcessDefinition.id ], { queryParamsHandling: 'merge' });
  }
}

