import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {
  HomeComponent, NewProcessInstanceComponent, ProcessInstanceListComponent, WorkItemListComponent,
  ProcessInstanceDetailComponent, WorkItemDetailComponent, NavigationTabs
} from './index';
import { ProcessDefinitionDetailComponent } from './process-definition-detail/process-definition-detail.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { DashboardComponent } from './dashboard/dashboard/dashboard.component';

const routes: Routes = [
  { path: '',  pathMatch: 'full', component: HomeComponent, data: {tab: NavigationTabs.Home} },
  { path: 'home', component: HomeComponent, data: {tab: NavigationTabs.Home} },
  { path: 'newProcess', component: NewProcessInstanceComponent, data: {tab: NavigationTabs.NewProcess} },
  { path: 'wiList', component: WorkItemListComponent, data: {tab: NavigationTabs.Tasks} },
  { path: 'piList', component: ProcessInstanceListComponent, data: {tab: NavigationTabs.Processes} },
  { path: 'pi/:idInst', component: ProcessInstanceDetailComponent, data: {tab: undefined} },
  { path: 'wi/:wiId', component: WorkItemDetailComponent, data: {tab: undefined} },
  { path: 'pd/:pdId', component: ProcessDefinitionDetailComponent, data: {tab: undefined} },
  { path: 'dashboard', component: DashboardComponent, data: {tab: NavigationTabs.Dashboard} },
  { path: '**', component: PageNotFoundComponent, data: {tab: undefined} },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {
    enableTracing: false,
    useHash: true
})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
