import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { WarningDialogComponent } from './warning-dialog/warning-dialog.component';
import { TimedoutDialogComponent } from './timedout-dialog/timedout-dialog.component';
import { FormsModule } from '@angular/forms';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { HttpTranslateLoaderFactory } from '../shared/http-translate-loader.factory';
import { HttpClient } from '@angular/common/http';



@NgModule({
  declarations: [
    WarningDialogComponent,
    TimedoutDialogComponent,
  ],
  imports: [
    CommonModule,
    FormsModule,
    NgbModule,
    TranslateModule.forChild({
      loader: {
        provide: TranslateLoader,
        useFactory: (HttpTranslateLoaderFactory),
        deps: [HttpClient]
      }
    }),
  ],
  exports: [
    WarningDialogComponent,
    TimedoutDialogComponent,
  ],
  entryComponents: [
    WarningDialogComponent,
    TimedoutDialogComponent,
  ],
})
export class IdleTimeOutModule { }
