import { Component, EventEmitter, Input, Output } from '@angular/core';
import { BpmFilterByListField } from 'ng-bpm';
import { NgbDate, NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { FilterDateService, RangeType } from './filter-date.service';
import { DateRange } from '../../dates/dates.service';

@Component({
  selector: 'pup-filter-date',
  templateUrl: './filter-date.component.html',
  styleUrls: ['./filter-date.component.css']
})
export class FilterDateComponent {
  @Input() listField;
  // This is an array because a date range needs two filters: from and to
  @Output() filtersChange = new EventEmitter<BpmFilterByListField[]>();

  public RangeTypeEnum = RangeType;
  public rangeType: RangeType = RangeType.NONE;

  public basicDate: NgbDateStruct;
  public dateRange: DateRange;

  constructor(
    private filterDateService: FilterDateService
  ) { }

  public rangeTypeChange() {
    if (this.rangeType === RangeType.NONE) {
      this.filtersChange.emit([]);
    }
    if (this.rangeType === RangeType.RANGE) {
      this.filtersChange.emit(this.filterDateService.getDateRangeFilters(this.dateRange, this.listField));
    }
    if ((this.rangeType === RangeType.SINCE) || (this.rangeType === RangeType.UNTIL)) {
      this.basicDateChange();
    }
  }

  public basicDateChange() {
    this.filtersChange.emit(this.filterDateService.getBasicDateFilters(this.basicDate, this.rangeType, this.listField));
  }

  public dateRangeChange(dateRange: {from: NgbDate, to: NgbDate}) {
    // This is called from a change on the component.
    this.dateRange = dateRange;
    this.filtersChange.emit(this.filterDateService.getDateRangeFilters(this.dateRange, this.listField));
  }

}
