import { Component, OnInit, Input, Injector, OnChanges, EventEmitter, Output } from '@angular/core';
import _ from 'underscore'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { DashboardModalPopupComponent } from '../dashboard-modal-popup/dashboard-modal-popup.component';
import { faTimes } from '@fortawesome/free-solid-svg-icons';
@Component({
  selector: 'pup-dashboard-modal-panel',
  templateUrl: './dashboard-modal-panel.component.html',
  styleUrls: ['./dashboard-modal-panel.component.css']
})
export class DashboardModalPanelComponent implements OnInit, OnChanges {
  imgpanel = [];
  @Input() modalpanel;
  parentdb;
  mpanels;
  linkpanel;
  animationsEnabled = true;
  lpanel = { isCustomChart: false };
  @Input() paneledit;
  @Output() removeFooterPanel = new EventEmitter();

  public faTimes = faTimes;

  constructor(
    private modalService: NgbModal,
    private injector: Injector
  ) { }

  ngOnChanges() {
    this.ngOnInit();
  }
  ngOnInit() {
    // this.modalpanel = this;
    this.lpanel['isCustomChart'] = false;
    this.loadBottomPanel(this.modalpanel);
  }

  loadBottomPanel(panel) {
    this.linkpanel = [];
    this.mpanels = panel;
    _.forEach(this.mpanels, (value) => {
      if (value.type == 3) {
        this.imgpanel.push(value);
      } else {
        this.linkpanel.push(value);
      }
    });
  }

  open(size, id) {
    this.loadChartPopup(size, id);
  }

  toggleAnimation() {
    this.animationsEnabled = !this.animationsEnabled;
  }

  loadChartPopup(sizeWin, id) {
    // $scope.pid = id;
    // var modalInstance = $uibModal.open({
    //   animation: this.animationsEnabled,
    //   ariaLabelledBy: 'modal-title',
    //   ariaDescribedBy: 'modal-body',
    //   templateUrl: 'gui/dashboard/modalPopup.html',
    //   controller: 'CommonChartsModalCtrl',
    //   controllerAs: 'convchart',
    //   size: size,
    //   resolve: {
    //     chartPanel: function () {
    //       return { id: id, modalInstance: this, isPopup: true };
    //     }
    //   }
    // });

    // modalInstance.result.then(function (selectedItem) {
    // }, function () {
    //   $log.info('Modal dismissed at: ' + new Date());
    // });

    this.modalService = this.injector.get(NgbModal);
    const modalRef = this.modalService.open(DashboardModalPopupComponent,
      { ariaLabelledBy: 'modal-basic-title', size: sizeWin, scrollable: true });
    modalRef.componentInstance.id = id;
    modalRef.componentInstance.isPopUp = true;
  }

  removeBottomPanel(panel) {
    this.removeFooterPanel.emit(panel);
  }

}
