import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class ShowUdasService {

  public getUdasCellRenderer(): any {
      return {
        headerName: "UDAs",
        field: "uda",
        sortable: true,
        filter: true,
        cellRenderer: data => {
          return JSON.stringify(data.value);
        },
        tooltipValueGetter: (p: any) => {
          return JSON.stringify(p.value);
        }
      };
  }

  public hasUdaCriteria(searchFilter): boolean {
    console.log('hasUdaCriteria', searchFilter);
    if (searchFilter.filterBy && searchFilter.filterBy.find(filter => filter.udaName)) {
        return true;
    }
    if (searchFilter.sortBy && searchFilter.sortBy.find(filter => filter.udaName)) {
      return true;
    }
    return false;
  }
}
