import { Injectable } from '@angular/core';
import _ from 'underscore';

@Injectable({
  providedIn: 'root'
})
export class PaginationService {

  constructor() { }

  getPage(totalItems, currentPage, pageSize) {
    currentPage = currentPage || 1;
    pageSize = pageSize || 10;
    const totalPages = Math.ceil(totalItems / pageSize);
    let startPage;
    let endPage;
    if(currentPage >= totalPages){
        currentPage = totalPages;
    }

    if (totalPages <= 10) {
        startPage = 1;
        endPage = totalPages;
    } else { // more than 10 total pages, calculate start and end pages
        if (currentPage <= 6) {
            startPage = 1;
            endPage = 10;
        } else if (currentPage + 4 >= totalPages) {
            startPage = totalPages - 9;
            endPage = totalPages;
        } else {
            startPage = currentPage - 5;
            endPage = currentPage + 4;
        }
    }

    const startIndex = (currentPage - 1) * pageSize;
    const endIndex = Math.min(startIndex + pageSize - 1, totalItems - 1);
    const pages = _.range(startPage, endPage + 1);
    return {
        totalItems: totalItems,
        currentPage: currentPage, pageSize: pageSize,
        totalPages: totalPages, startPage: startPage, endPage: endPage,
        startIndex: startIndex, endIndex: endIndex, pages: pages
    }
}

}
