import { Component, OnInit } from '@angular/core';
import { ProcessDefinitionFilter, BpmListCriteria } from 'ng-bpm';
import {
  PupProcessDefinitionNameColumnComponent,
  PupProcessDefinitionStateComponent
} from '../shared/component/process-definition.component';
import { BpmServerService } from '../shared/bpm-server/bpm-server.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'pup-new-process-instance',
  templateUrl: './new-process-instance.component.html',
  styleUrls: ['./new-process-instance.component.css']
})
export class NewProcessInstanceComponent implements OnInit {
  public processDefinitions$;
  public filter = ProcessDefinitionFilter.AllPlans;
  public gridPageSizeValue: number;
  pageDetail;
  searchFilter: BpmListCriteria = new BpmListCriteria();
  constructor(
    private bpmServerService: BpmServerService,
  ) { }

  private columnDefsSingle: any[] = [{
    headerName: 'Id',
    field: 'id',
    sortable: true,
    filter: true,
  },
  {
    headerName: 'Name',
    field: 'name',
    sortable: true,
    filter: true,
    cellRendererFramework: PupProcessDefinitionNameColumnComponent,
  },
  {
    headerName: 'Version',
    field: 'version',
    sortable: true,
    filter: true

  },
  {
    headerName: 'State',
    field: 'state',
    sortable: true,
    filter: true,
    cellRendererFramework: PupProcessDefinitionStateComponent,
  },
  {
    headerName: 'Owners',
    field: 'owner',
    sortable: true,
    filter: true
  }];
  public columnDefs: any[] = this.columnDefsSingle;
  private columnDefsMulti: any[] = this.columnDefsSingle.concat([{
    headerName: 'Server',
    field: 'serverId',
    sortable: true,
    filter: true
  }]);

  ngOnInit() {
    this.searchFilter.includeCount = true;
    this.searchFilter.batchSize = Number(localStorage.getItem('pageSize')) || environment.gridPageSize;
    if (this.bpmServerService.isMulti()) {
      console.log('Multi server');
      // Display application name in the grid.
      this.columnDefs = this.columnDefsMulti;
    }
    this.getlistProcessDefinition(this.searchFilter);
  }

  getlistProcessDefinition(searchFilter) {
    this.processDefinitions$ = this.bpmServerService.getProcessDefinitions(this.filter, searchFilter);
    this.processDefinitions$.subscribe(result => {
      this.pageDetail = { totalItems: result.totalCount, pageSize: this.searchFilter.batchSize, filter: this.filter };
    })
  }

  updateGridPageSize(pageCount) {
    this.gridPageSizeValue = pageCount;
    localStorage.setItem('pageSize', this.gridPageSizeValue.toString());
    this.searchFilter.batchSize = this.gridPageSizeValue;
    this.searchFilter.batchOffset = 0;
    this.getlistProcessDefinition(this.searchFilter);
  }

  public broadcastPageInfo(pageInfo) {
    this.searchFilter.batchOffset = pageInfo.pager.startIndex;
    this.getlistProcessDefinition(this.searchFilter);
  }
}
